import util from '../util/util';
import consts from '../consts/consts';
/**
 * Clipboard action Class.<br/>
 * copy, cut, paste API .
 */

class SEClipboard {
    constructor(editor) {
        this.editorId = editor.id;
        this.selectionManager = editor.getSelectionManager();
        this.modelHandler = editor.getModelHandler();
        this.positionManager = editor.getPositionManager();
        this.selection = this.selectionManager.selection;
        this.clipboardFrame = this.getClipboardFrame();
        this.browser = util.getUserAgent(window.navigator.userAgent);
        this.copiedParagraphs = null;
    }

    /**
     * copy, cut, paste   iFrame . <br/>
     * document se-clipboard iFrame    ,  DOM iFrame  . <br/>
     *  iframe 'synapEditor' append .
     * @returns {HTMLElement} se-clipboard
     */
    getClipboardFrame() {
        if (!document.getElementsByClassName('se-clipboard')[0]) {
            const elClipboardFrame = document.createElement('IFRAME');
            elClipboardFrame.className = 'se-clipboard';
            document.getElementById(this.editorId).appendChild(elClipboardFrame);
            elClipboardFrame.contentDocument.body.contentEditable = 'true';
            return elClipboardFrame;
        }
        return document.getElementsByClassName('se-clipboard')[0];
    }

    /**
     * copy, cut, paste  iFrame document    .
     * @param {HTMLElement} document
     */
    setSelection(doc) {
        const selection = doc.getSelection();
        const range = doc.createRange();
        range.selectNodeContents(doc.body);
        selection.removeAllRanges();
        selection.addRange(range);
    }

    /**
     * event     .<br/>
     *  browser   .
     * @param {Event} originalEvent 
     */
    copy(event) {
        let paraElements = [];
        this.copiedParagraphs = this.selection.getCopiedParagraphs();
        this.selection.getParagraphIds().forEach(id => paraElements.push(document.querySelector(`[para-id="${id}"]`).outerHTML));
        if (this.browser === 'Chrome') {
            this.copyByClipboardData(event, paraElements.join(''));
        } else if (this.browser === 'MSIE') {
            this.copyByHooking(paraElements.join(''));
        }
    }

    /**
     * jQuery originalEvent   clipboardData text/html ||text type type    iframe .
     * @param {Event} originalEvent 
     * @param @param {String} contents 
     */
    copyByClipboardData(event, contents) {
        this.setClipboardData(contents);
        event.clipboardData.setData('text/html', this.clipboardFrame.contentDocument.body.innerHTML);
        event.clipboardData.setData('text', this.clipboardFrame.contentDocument.body.innerText);
        this.clipboardFrame.contentDocument.body.innerHTML = '';
    }

    /**
     * clipboardData    copyByHooking . <br/>
     *      clipboardFrame <br/>
     *   , iframe contents     <br/>
     * copy command .<br/>
     * execCommand('copy')    clipboardFrame  .<br/>
     * copy command    iframe .
     * @param {any} contents 
     */
    copyByHooking(contents) {
        this.setClipboardData(contents);
        this.setSelection(this.clipboardFrame.contentDocument);
        this.clipboardFrame.contentDocument.execCommand('copy');
        this.clipboardFrame.contentDocument.body.innerHTML = '';
    }

    /**
     * iFrame    . <br/>
     * iFrame     paragraph contents  <br/>
     *      clone   synapEditor  .
     * @param {String} contents 
     */
    setClipboardData(contents) {
        const doc = this.clipboardFrame.contentDocument || this.clipboardFrame.contentWindow;
        doc.body.innerHTML = contents;

        let startPosition = this.selection.getStartPosition();
        let endPosition = this.selection.getEndPosition();
        let startOffset = startPosition.offset;
        let endOffset = endPosition.offset;

        const selection = doc.getSelection();
        const range = doc.createRange();
        let firstPara = doc.body.firstChild;
        let lastPara = doc.body.lastChild;
        let startNodeInfo = this.getNodeInfo(firstPara, startOffset);
        let endNodeInfo = this.getNodeInfo(lastPara, endOffset);
        range.setStart(firstPara.children[startNodeInfo.index].firstChild, startNodeInfo.offset);
        range.setEnd(lastPara.children[endNodeInfo.index].firstChild, endNodeInfo.offset);

        selection.removeAllRanges();
        let clonedContents = range.cloneContents();

        let elTmp = document.createElement('div');
        elTmp.id = `${consts.SE_CLIPBOARD}_${this.editorId}`;
        elTmp.appendChild(clonedContents);

        refineMarkup.replaceHref(elTmp);
        doc.body.innerHTML = '';
        doc.body.appendChild(elTmp);
    }

    /**
     *    NodeInfo . <br/>
     * index ParagraphElement   children  . 
     * offset textNode offset .
     * @param {HTMLElement} elPara 
     * @param {Number} offset 
     * @returns {Object} NodeInfo
     */
    getNodeInfo(elPara, offset) {
        let idx = 0;
        let revisedOffset = 0;
        let index = Array.from(elPara.children).findIndex(child => {
            let textLength = child.textContent.length;
            if (idx <= offset && offset <= idx + textLength) {
                revisedOffset = offset - idx;
                return true;
            } else {
                idx += textLength;
            }
        });
        return {index: index, offset: revisedOffset};
    }

    /**
     * event     .<br/>
     *  browser   .
     * @param {Event} originalEvent 
     */
    paste(event) {
        if (this.browser === 'Chrome') {
            this.pasteByClipboardData(event);
        } else if (this.browser === 'MSIE') {
            this.pasteByHooking();
        }
    }

    /**
     * jQuery originalEvent   clipboardData  text/html ||text type type  pasteHandler .
     * @param {Event} originalEvent 
     * @returns {String} text/html || text
     */
    pasteByClipboardData(event) {
        let contents = event.clipboardData.getData('text/html') || event.clipboardData.getData('text');
        this.pasteHandler(contents);
    }

    /**
     * clipboardData    pasteByHooking . <br/>
     * execCommand('paste')    clipboardFrame body . <br/>
     * body innerHTML pasteHandler   .
     */
    pasteByHooking() {
        let elClipboardBody = this.clipboardFrame.contentWindow.document.body;
        elClipboardBody.innerHTML = '';
        elClipboardBody.focus();
        this.clipboardFrame.contentWindow.document.execCommand('paste');
        this.pasteHandler(elClipboardBody.innerHTML);
    }

    /**
     * clipboard  hooking  html  . <br/>
     * handler  html  htmlToModel convert  .
     * @param {String} html || text
     */
    pasteHandler(html) {
        const elTmp = document.createElement('div');
        elTmp.innerHTML = html;
        const elSEClipboardData = elTmp.querySelector(`#${consts.SE_CLIPBOARD}_${this.editorId}`);
        let paragraphs;

        if (elSEClipboardData && this.copiedParagraphs) {
            paragraphs = this.copiedParagraphs.slice(0);
        } else {
            // htmlToModel(html) -> builder -> paste
        }

        if (paragraphs.length === 0) { //   0     .
            return;
        }

        this.modelHandler.pasteModel(paragraphs);
    }
}

/**
 * clipboard  content        .
 */
export const refineMarkup = {
    // IE     href se-data-href ,  se-href href 
    replaceHref: function(elements) {
        Array.from(elements.getElementsByTagName('A')).forEach(aElement => {
            const href = aElement.getAttribute('se-data-href');
            if (href) {
                aElement.href = href;
                aElement.removeAttribute("se-data-href");
            }
        })
    }
};

export default SEClipboard;


