/**
 * SEModel    .
 */
class IndexManager {
    constructor() {
        this.indices = {};
    }
    create(name) {
        return (this.indices[name] = new Index());
    }
    getAll() {
        return Object.keys(this.indices).map(i => this.indices[i]);
    }
    get(name) {
        return this.indices[name] || this.create(name);
    }
    mergeWith(another) {
        if (!another) {
            return;
        }
        for (let name in another.indices) {
            let anotherIndex = another.indices[name].__index__;
            if (!this.indices[name]) {
                this.create(name);
            }
            let index = this.indices[name].__index__;
            for (let key in anotherIndex) {
                index[key] = anotherIndex[key];
            }
        }
    }
}

class Index {
    constructor() {
        this.__index__ = {};
    }

    //  :   map, filter  => for 
    // 2 : loop   .
    getAll(container) {
        let keys = Object.keys(this.__index__);
        let values = [];
        let index = 0;
        for (let i = 0; i < keys.length; i++) {
            let key = keys[i];
            let value = this.__index__[key];
            if (!container || (container && container.contains(value))) {
                values[index++] = value;
            }
        }
        return values;
    }
    get(key, container) {
        var value = this.__index__[key];
        if (value) {
            let valueId = value.getId();
            if (valueId !== key && this.__index__[valueId] !== this.__index__[key]) {
                //          .
                //      .
                //     Element    Element       
                console.info(' Element  %s %s ', key, valueId);
                value = this.__index__[key] = this.__index__[valueId];
            }
            if (container && !container.contains(value)) {
                //   container      
                this.remove(key);
                return null;
            }
        }
        return value || null;
    }
    set(key, value) {
        this.__index__[key] = value;
    }
    remove(key) {
        delete this.__index__[key];
    }
}
export default IndexManager;


