import Element from './Element';
import TextRun from './TextRun';

const DrawingObjectLength = 1;

/**
 * DrawingObject
 * @extends Element
 */
class DrawingObject extends Element {
    /**
     * Element Type .
     * @returns {String} Body String
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'DrawingObject';
    }

    length() {
        return DrawingObjectLength;
    }

    /**
     *  export , SEModel docModel    .<br/>
     * DrawingObject Element TextRun    , <br/>
     *  textrun  docModel textrun .<br/>
     *  docModel  drawingobject  JSON Object .
     * @returns {Object} drawingobject JSON Object
     */
    toPbModel(graphicObj) {
        let textrun = {
            "textrun": {
                "drawing_obj": {
                    "element_id": this.element_id,
                    "layout_prop": this.layout_prop,
                    "graphic_obj": graphicObj
                }
            },
            "type": "TEXTRUN"
        };

        if (this.textProp) { //   textrun prop undefined
            textrun.textrun.prop = this.textProp;
        }
        return textrun;
    }

    /**
     * DrawingObject split index   TextRun  .
     * @param {Number} index
     * @returns {TextRun} TextRun
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default DrawingObject;


