import Position from './Position';
import consts from '../../consts/consts';

const {POSITION_TYPE} = consts;

/**
 *     class.
 */
class PicturePosition extends Position {
    /**
     * id, offset, rowIndex, colIndex  .
     * @param {Number} id table element id
     * @param {Number} offset  
     * @param {Number} pictureId picture element id
     */
    constructor(id, offset, pictureId) {
        super(id, offset);
        this.pictureId = pictureId;
    }

    /**
     *  type .
     * @returns {String}
     */
    getType() {
        return POSITION_TYPE.PICTURE;
    }

    /**
     * cell position   .
     * @returns {PicturePosition} Clone PicturePosition
     */
    clone() {
        return new PicturePosition(this.id, this.offset, this.pictureId);
    }

    /**
     * id, offset, pictureId   Position .
     * @param {Position} position
     * @returns {Boolean}
     */
    isEqual(position) {
        return super.isEqual(position) && this.pictureId === position.pictureId;
    }
}

export default PicturePosition;


