import $ from 'jquery';
import shortcut from './shortcut';
import ACTION from './consts/actionMap';
import util from './util/util';


/**
 *    Class. <br/>
 * browser key event  . <br/>
 * Ime Element textarea  keyDown, input  . <br/>
 */
class Ime {
    constructor(editorId, controller) {
        this.controller = controller;
        this.editorId = editorId;
        this.$elImeContainer = $(`#${this.editorId} .ime-container`);
        this.$elIme = $(`#${this.editorId} .ime`);
        this.disabled = false;
        this.prevInput = '';

        this.init();
    }

    /**
     * Ime  .
     */
    init() {
        if (!this.$elImeContainer.length) {
            // ime  display none, visibility hidden   event   ime-container
            // .
            this.$elImeContainer = $('<div class="ime-container"></div>');
            this.$elIme = $('<textarea class="ime"></textarea>');
            this.$elImeContainer.append(this.$elIme);
            $(`#${this.editorId} .se-main`).append(this.$elImeContainer);
        }
        this.$elIme.on('input keydown copy paste', this, this.bindEvent);
    }

    /**
     * Ime HTML Element focus .
     */
    focus() {
        this.$elIme.focus();
    }

    /**
     * Ime HTML Element blur . (focus )
     */
    blur() {
        this.$elIme.blur();
    }

    /**
     *   . (keyDown, input, paste)
     * @param {Event} e
     */
    bindEvent(e) {
        if (this.disabled) {
            e.preventDefault();
        } else {
            switch (e.type) {
                case 'keydown':
                    let isMetaCtrl = e.originalEvent.ctrlKey || e.originalEvent.metaKey;
                    if (isMetaCtrl && e.originalEvent.keyCode === 67 && util.getUserAgent(window.navigator.userAgent) === 'MSIE') { // ie copy event binding  copy  .
                        onCopy(e);
                        break;
                    } else if (isMetaCtrl && (e.originalEvent.keyCode === 86 || e.originalEvent.keyCode === 67)) { // copy paste keydown     .
                        return;
                    }
                    onKeyDown(e);
                    break;
                case 'input':
                    onInput(e);
                    break;
                case 'copy':
                    onCopy(e);
                    break;
                case 'paste':
                    onPaste(e);
                    break;
            }
        }
    }

    /**
     * Ime ( ) .<br/>
     * enter, shortcut    .
     */
    resetIme() {
        this.prevInput = '';
        this.$elIme.val('');
    }

    /**
     *   left, top  Ime Contaeinr  .
     * @param {Number} left 
     * @param {Number} top 
     */
    moveIme(left, top) {
        // Ime  
        this.$elImeContainer.css({
            left: left,
            top: top
        });
    }
}

/**
 * Ime keydown  . <br/>
 * event  action   controller   .
 * @param {Event} e
 */
function onKeyDown(e) {
    const ime = e.data;
    const action = shortcut(e);
    if (!!action) {
        e.preventDefault();
        e.stopPropagation();

        ime.controller.execute(action);
        ime.resetIme();
    }
}

/**
 * Ime input  . <br/>
 * controller  ACTION.INSERT_TEXT .
 * @param {Event} e
 */
function onInput(e) {
    const ime = e.data;
    const text = ime.$elIme.val(), prevInput = ime.prevInput;
    let same = 0, l = Math.min(prevInput.length, text.length);
    while (same < l && prevInput.charCodeAt(same) == text.charCodeAt(same)) {
        ++same;
    }

    const updateTextLength = prevInput.length - same;
    const insertText = text.slice(same);

    ime.controller.execute(ACTION.INSERT_TEXT, insertText, updateTextLength);
    ime.prevInput = text;
}

/**
 * Ime paste  . <br/>
 * controller  ACTION.PASTE .
 * @param {Event} e 
 */
function onPaste(e) {
    const ime = e.data;
    e.preventDefault();
    e.stopPropagation();
    ime.controller.execute('paste', e);
}

/**
 * Ime copy  . <br/>
 * controller  ACTION.COPY .
 * @param {Event} e 
 */
function onCopy(e) {
    const ime = e.data;
    e.preventDefault();
    e.stopPropagation();
    ime.controller.execute('copy', e);
}

export default Ime;


