import util from '../../util/util';
import consts from '../../consts/consts';
import HTMLToModel from './HTMLToModel';

const LISTS_FORMAT = consts.LISTS_FORMAT;
const LISTS_LEVEL_TEXT = consts.LISTS_LEVEL_TEXT;

/*
 *    
 * [, , , , , , , , ]
 */
const BULLET_DEFAULT_FORMAT = [
    LISTS_LEVEL_TEXT.DISC,
    LISTS_LEVEL_TEXT.CIRCLE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE,
    LISTS_LEVEL_TEXT.SQUARE
];

/*
 *    
 * [1, 1, 1, 1, 1, 1, 1, 1, 1]
 */
const NUMBERING_DEFAULT_FORMAT = [
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL,
    LISTS_FORMAT.DECIMAL
];

const LISTS_TYPE = consts.LISTS_TYPE;
const LIST_STYLE_TYPE = {
    'decimal': LISTS_FORMAT.DECIMAL,
    'lower-roman': LISTS_FORMAT.LOWER_ROMAN,
    'upper-roman': LISTS_FORMAT.UPPER_ROMAN,
    'lower-alpha': LISTS_FORMAT.LOWER_LETTER,
    'upper-alpha': LISTS_FORMAT.UPPER_LETTER
};


/**
 * CK EDITOR Publishing  docModel  
 * @extends HTMLToModel
 */
class HTMLToModelForCK extends HTMLToModel {
    /** 
     * Model     .
     * @private
     */
    __init__() {
        super.__init__();

        // numberingSeq :   .   .
        // listRoot :   .
        this.numberingSeq = 0;
        this.listRoot = undefined;

        this.tagList['OL'] = 'LIST';
    }

    /**
     *   (LI) Paragraph   .
     * @private
     * @param {HTMLElement} listElement  Element (UL)
     * @param {Number} numberingId  ID
     * @returns {Array} Paragraphs
     */
    __getListParagraphs__(listElement, numberingId) {
        this.listRoot = listElement;

        const paragraphs = [];
        const remains = [];

        for (let i = listElement.children.length - 1; i >= 0; i--) {
            remains.push(listElement.children[i]);
        }

        let childElement;
        while (remains.length > 0) {
            childElement = remains.pop();

            switch (this.tagList[childElement.nodeName]) {
                case 'PARAGRAPH':
                    paragraphs.push(this.convertToParagraph(childElement, numberingId));
                    break;
            }

            const childListElement = this.__getChildListElement__(childElement);
            if (!!childListElement) {
                for (let i = childListElement.childNodes.length - 1; i >= 0; i--) {
                    remains.push(childListElement.childNodes[i]);
                }
            }
        }

        this.listRoot = undefined;

        return paragraphs;
    }

    /**
     *   Element(LI)  Element(UL/OL)  .
     * @private
     * @param {HTMLElement} liElement 
     * @returns {HTMLElement|undefined}  Element (UL/OL)
     */
    __getChildListElement__(liElement) {
        const childNodes = liElement.childNodes;

        for (let i = 0, len = childNodes.length; i < len; i++) {
            if (this.tagList[childNodes[i].nodeName] === 'LIST') {
                return childNodes[i];
            }
        }
    }

    /**
     *     .
     * @private
     * @param {HTMLElement} listElement LIST HTML Element (UL/OL)
     * @returns {Object}  
     */
    __getListProp__(listElement) { //  element  
        const listProp = {};

        listProp.numberingId = ++this.numberingSeq;
        listProp.multiLevelType = 'hybridMultilevel';

        return listProp;
    }

    /**
     *      .
     * @private
     * @param {HTMLElement} element Paragraph HTML Element
     * @returns {Object}     
     */
    __getNumberingLevelProp__(element) {
        const numberingProp = {};
        const parentElement = element.parentElement;
        const parentStyle = parentElement.style;
        const level = this.__getListLevel__(element);
        const isUL = parentElement.nodeName === 'UL';
        const formatList = isUL ? BULLET_DEFAULT_FORMAT : NUMBERING_DEFAULT_FORMAT;

        numberingProp.level = level;

        numberingProp.startValue = !!parentElement.getAttribute('start') ? parseInt(parentElement.getAttribute('start')) : 1;

        let format, levelText;
        const listStyleType = parentStyle.listStyleType;
        if (!!listStyleType) {
            if (isUL) {
                // UL  format 'bullet' levelText 
                levelText = LISTS_LEVEL_TEXT[listStyleType.toUpperCase()];
            } else {
                // OL format 
                format = LIST_STYLE_TYPE[listStyleType];
            }
        }

        numberingProp.format = !!format ? format : (isUL ? LISTS_TYPE.BULLET : formatList[level]);

        numberingProp.levelText = !!levelText ? levelText : (isUL ? formatList[level] : `%${level + 1}.`);

        return numberingProp;
    }

    /**
     *  (UL/OL)    Element(li)  .
     * @private
     * @param {HTMLElement} liElement   Element
     * @returns {Number} 
     */
    __getListLevel__(liElement) {
        let level = 0;

        let parent = liElement;
        while (!!parent.parentElement) {
            parent = parent.parentElement;

            if (this.tagList[parent.nodeName] === 'LIST') {
                level++; // UL/OL  1 
            }

            if (parent === this.listRoot) {
                break;
            }
        }

        return level - 1;
    }

    /**
     * HTML  .<br/>
     *     .<br/>
     *        .
     * @private
     * @param {HTMLElement} element   HTML Element
     * @returns {String} HTML 
     */
    __getTextContent__(element) {
        return super.__getTextContent__(element).replace(/\n/g, '').replace(/^\t+/, '');
    }

    /**
     * Paragraph  Element 
     * @private
     * @param {HTMLElement} parentElement 
     * @param {Array} [targetChildren]   Paragraph .<br/>  parentElement  Paragraph childNodes .
     * @returns {Array} textrun
     */
    __getParagraphChildren__(parentElement, targetChildren) {
        const childrenElement = [];
        const remains = [];

        const children = targetChildren || parentElement.childNodes;
        for (let i = children.length - 1; i >= 0; i--) {
            remains.push(children[i]);
        }

        while (remains.length > 0) {
            const curr = remains.pop();
            const type = this.tagList[curr.nodeName];

            if (type === 'LIST') {
                continue; //  
            }

            for (let i = curr.childNodes.length - 1; i >= 0; i--) {
                remains.push(curr.childNodes[i]);
            }

            if (util.isTextNode(curr) && !!this.__getTextContent__(curr)) {
                childrenElement.push(this.convertToTextRun(curr, parentElement));
            } else if (type === 'BREAK') {
                childrenElement.push(this.__getBreakTextRun__());
            } else if (type === 'PICTURE') {
                childrenElement.push(this.convertToDrawingObj(curr));
            }
        }

        return childrenElement;
    }

    /**
     * Table Cell Paragraph  .
     * @private
     * @param {HTMLElement} tdElement TD
     * @returns {Array} Paragraphs
     */
    __getTableCellParagraphs__(tdElement) {
        const paragraphs = [];
        const length = tdElement.childNodes.length;

        if (length === 0) {
            //  node   Paragraph 
            tdElement.appendChild(this.__createEmptyParaElement__());
        }

        let paraChildren = [];
        const count = length - 1;

        Array.from(tdElement.childNodes).forEach((child, idx) => {
            const para = this.__getTableCellParagraphObject__(child);
            if (!para) {
                // getTableCellParagraphObject    
                //   Paragraph     .
                paraChildren.push(child);
            }

            if ((!!para && paraChildren.length > 0) || idx === count) {
                // paraChildren  element   paragraph .
                const children = this.__getParagraphChildren__(tdElement, paraChildren);
                if (children.length > 0) {
                    const paragraph = this.__getDefaultParagraphObject__().paragraph;
                    paragraph.children = children;
                    paragraphs.push(paragraph);
                }

                paraChildren = [];
            }

            if (!!para) {
                if (Array.isArray(para)) {
                    paragraphs.push(...para);
                } else {
                    paragraphs.push(para);
                }
            }
        });

        return paragraphs;
    }

    /**
     *      css      .
     * @private
     * @param {TextNode} textNode 
     * @param {HTMLElement} rootElement    Element
     * @returns {Object} textrun prop
     */
    __getTextRunProp__(textNode, rootElement) {
        const prop = {};

        if (!textNode || !rootElement) {
            return prop;
        }

        let curr = textNode;
        while (!!(curr = curr.parentElement) && curr !== rootElement) {
            switch (curr.nodeName) {
                case 'EM':
                    prop.italic = true;
                    break;
                case 'S':
                    prop.strike = true;
                    break;
                case 'STRONG':
                    prop.bold = true;
                    break;
                case 'U':
                    prop.underline = true;
                    break;
                case 'SPAN':
                    Object.assign(prop, super.__getTextRunProp__(curr));
                    break;
            }
        }

        return prop;
    }

    /**
     * style      .
     * @private
     * @param {HTMLElement} tableElement TABLE Element
     * @returns {Object} table prop
     */
    __getTableProp__(tableElement) {
        const tableProp = {};

        //CK Editor TABLE Element style width ,
        //       auto  .
        tableProp.isAutoWidth = true;

        return tableProp;
    }

    /**
     * style      .
     * @private
     * @param {HTMLElement} tdElement TD Element
     * @returns {Object} tableCell prop
     */
    __getTableCellProp__(tdElement) {
        const tableCellProp = super.__getTableCellProp__(tdElement);
        const parentTable = this.__getTableInfo__().table;

        // table border width 0 ,   border .
        // : CK  table border width   table Element 
        const borderWidth = parseInt(parentTable.getAttribute('border'));
        if (borderWidth > 0) {
            ['Top', 'Bottom', 'Left', 'Right'].forEach((positionStr) => {
                let borderProp = util.get(tableCellProp, [`border${positionStr}`]);
                if (!borderProp) {
                    borderProp = tableCellProp[`border${positionStr}`] = {};
                }

                if (!('style' in borderProp)) {
                    borderProp.style = 'single'; // 1 
                }

                if (!('color' in borderProp)) {
                    borderProp.color = 0; //  
                }

                if (!('width' in borderProp)) {
                    borderProp.width = util.px2sunit(borderWidth);
                }
            });
        }

        return tableCellProp;
    }
}

export default HTMLToModelForCK;


