import Element from './Element';
 
/**
 * Body
 * @extends Element
 */
class Body extends Element {

    /**
     * childrenPropety .
     * @private
     * @returns {String} children
     */
    __getChildrenProperty__() {
        return 'children';
    }

    /**
     * Element Type .
     * @returns {String} Body
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'Body';
    }
}

export default Body;


