import TextRun from './TextRun';
import util from '../util/util';

/**
 * HyperlinkStart
 * @extends TextRun
 */
class HyperlinkStart extends TextRun {
    /**
     * HyperlinkStart  .
     * @param {Number} id
     * @param {String} url
     * @return {HyperlinkStart}
     */
    static createHyperlinkStart(id, url) {
        const hyperlinkStart = new HyperlinkStart({
            id: id,
            prop: {}
        });
        hyperlinkStart.setTarget(url);
        return hyperlinkStart;
    }

    /**
     * HyperlinkStart length .
     * @returns {Number}
     */
    length() {
        return 0;
    }

    /**
     * Element Type .
     * @returns {String} HyperlinkStart
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'HyperlinkStart';
    }

    /**
     * HyperlinkStart prop target .
     * @return {String} HyperlinkStart target (Url, Email)
     */
    getTarget() {
        return this.getProp().target;
    }

    /**
     * HyperlinkStart prop target .
     * @param {String} url 
     */
    setTarget(url) {
        const type = util.validateWebpageUrl(url) ? 'WEB' : util.validateEmailAdress(url) ? 'Email' : '';
        const target = (type === 'WEB' && url.indexOf('http') !== 0) ? 'http://' + url : url;

        this.prop.target = target;
        this.prop.type = type;
    }

    /**
     *  TextRun  index  HyperlinkStart    TextRun .
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default HyperlinkStart;


