import $ from 'jquery';
import ACTION from '../consts/actionMap';
import ControllerInterface from './ControllerInterface';

/**
 *    class.<br/>
 * editor ime, caret  .
 */
class SelectionController extends ControllerInterface {
    constructor(editor, name) {
        super();
        this.name = name;
        this.uiManager = editor.getUIManager();
        this.selectionRenderer = editor.getSelectionRenderer();
        this.selectionManager = editor.getSelectionManager();
        this.selection = editor.selectionManager.selection;
        this.controller = editor.getController();
        this.modelHandler = editor.getModelHandler();
        this.ime = editor.getIme();
        this.bSavePositionX = true;
    }

    /**
     *       x .
     */
    postExecute() {
        this.selectionRenderer.reviseScroll(this.selection.focus);
        this.selectionRenderer.renderSelection(this.selection);
        this.bSavePositionX && this.selectionManager.setStandardLeft();
        this.modelHandler.initEndRunProp();
    }
}

/**
 * selection    selectionController .
 *    SelectionController  .
 */
const selectionController = {
    /**
     * left key selection action .
     */
    [ACTION.LEFT]: class extends SelectionController {
        execute(){
            this.selectionManager.left();
        }
    },
    /**
     * right key selection action .
     */
    [ACTION.RIGHT]: class extends SelectionController {
        execute(){
            this.selectionManager.right();
        }
    },
    /**
     * up key selection action .
     */
    [ACTION.UP]: class extends SelectionController {
        execute(){
            this.bSavePositionX = false;
            this.selectionManager.up();
        }
    },
    /**
     * down key selection action .
     */
    [ACTION.DOWN]: class extends SelectionController {
        execute(){
            this.bSavePositionX = false;
            this.selectionManager.down();
        }
    },
    /**
     * shift+left key selection  .
     */
    [ACTION.EXTEND_LEFT]: class extends SelectionController {
        execute(){
            this.selectionManager.left(true);
        }
    },
    /**
     * shift+right key selection  .
     */
    [ACTION.EXTEND_RIGHT]: class extends SelectionController {
        execute(){
            this.selectionManager.right(true);
        }
    },
    /**
     * shift+up key selection  .
     */
    [ACTION.EXTEND_UP]: class extends SelectionController {
        execute(){
            this.bSavePositionX = false;
            this.selectionManager.up(true);
        }
    },
    /**
     * shift+down key selection  .
     */
    [ACTION.EXTEND_DOWN]: class extends SelectionController {
        execute(){
            this.bSavePositionX = false;
            this.selectionManager.down(true);
        }
    },
    /**
     * line    .
     */
    [ACTION.LINE_START]: class extends SelectionController {
        execute(){
            this.selectionManager.lineStart();
        }
    },
    /**
     * line    .
     */
    [ACTION.LINE_END]: class extends SelectionController {
        execute(){
            this.selectionManager.lineEnd();
        }
    },
    /**
     * line    .
     */
    [ACTION.EXTEND_LINE_START]: class extends SelectionController {
        execute() {
            this.selectionManager.lineStart(true);
        }
    },
    /**
     * line    .
     */
    [ACTION.EXTEND_LINE_END]: class extends SelectionController {
        execute() {
            this.selectionManager.lineEnd(true);
        }
    },
    /**
     *     .
     */
    [ACTION.DOCUMENT_START]: class extends SelectionController {
        execute() {
            this.selectionManager.documentStart();
        }
    },
    /**
     *     .
     */
    [ACTION.DOCUMENT_END]: class extends SelectionController {
        execute() {
            this.selectionManager.documentEnd();
        }
    },
    /**
     *     .
     */
    [ACTION.EXTEND_DOCUMENT_START]: class extends SelectionController {
        execute() {
            this.selectionManager.documentStart(true);
        }
    },
    /**
     *     .
     */
    [ACTION.EXTEND_DOCUMENT_END]: class extends SelectionController {
        execute() {
            this.selectionManager.documentEnd(true);
        }
    },
    /**
     *      .
     */
    [ACTION.PREVIOUS_PARAGRAPH_START]: class extends SelectionController {
        execute() {
            this.selectionManager.previousParagraphStart();
        }
    },
    /**
     *      .
     */
    [ACTION.NEXT_PARAGRAPH_START]: class extends SelectionController {
        execute() {
            this.selectionManager.nextParagraphStart();
        }
    },
    /**
     *      .
     */
    [ACTION.EXTEND_PREVIOUS_PARAGRAPH_START]: class extends SelectionController {
        execute() {
            this.selectionManager.previousParagraphStart(true);
        }
    },
    /**
     *      .
     */
    [ACTION.EXTEND_NEXT_PARAGRAPH_START]: class extends SelectionController {
        execute() {
            this.selectionManager.nextParagraphStart(true);
        }
    },
    /**
     *    .
     */
    [ACTION.SELECT_ALL]: class extends SelectionController {
        execute() {
            this.selectionManager.documentStart();
            this.selectionManager.documentEnd(true);
        }
    },
    /**
     *      .
     */
    [ACTION.PREVIOUS_WORD]: class extends SelectionController {
        execute() {
            this.selectionManager.previousWord();
        }
    },
    /**
     *      .
     */
    [ACTION.NEXT_WORD]: class extends SelectionController {
        execute() {
            this.selectionManager.nextWord();
        }
    },
    /**
     *      .
     */
    [ACTION.EXTEND_PREVIOUS_WORD]: class extends SelectionController {
        execute() {
            this.selectionManager.previousWord(true);
        }
    },
    /**
     *      .
     */
    [ACTION.EXTEND_NEXT_WORD]: class extends SelectionController {
        execute() {
            this.selectionManager.nextWord(true);
        }
    },
    /**
     *    .
     */
    [ACTION.PAGE_UP]: class extends SelectionController {
        execute() {
            this.bSavePositionX = false;
            this.selectionManager.pageUp();
        }
    },
    /**
     *    .
     */
    [ACTION.PAGE_DOWN]: class extends SelectionController {
        execute() {
            this.bSavePositionX = false;
            this.selectionManager.pageDown();
        }
    },
    /**
     *    .
     */
    [ACTION.EXTEND_PAGE_UP]: class extends SelectionController {
        execute() {
            this.bSavePositionX = false;
            this.selectionManager.pageUp(true);
        }
    },
    /**
     *    .
     */
    [ACTION.EXTEND_PAGE_DOWN]: class extends SelectionController {
        execute() {
            this.bSavePositionX = false;
            this.selectionManager.pageDown(true);
        }
    },
    /**
     *   caret .
     * @param {Number} x x
     * @param {Number} y y
     * @param {Boolean} [isExtend] selection 
     */
    [ACTION.SET_SELECTION]: class extends SelectionController {
        execute(x, y, isExtend = false) {
            this.selectionManager.setSelection(x, y, isExtend);
        }
        postExecute() {
            super.postExecute();
            this.ime.focus();
        }
    },
    /**
     *      .
     * @param {Number} x x
     * @param {Number} y y
     */
    [ACTION.SELECT_WORD]: class extends SelectionController {
        execute(x, y) {
            this.selectionManager.setSelection(x, y);
            this.selectionManager.previousWord();
            this.selectionManager.nextWord(true);
        }
    
    },

    [ACTION.SET_SELECTION_END]: class extends SelectionController {
        execute(element) {
            if ($(element).parents('.se-table-cell-prop').length > 0) {
                this.controller.execute(ACTION.SHOW_TABLE_CELL_BALLOON_POPUP);
            } else if (this.selection.isPictureSelection()) {
                const {focus} = this.selection;
                this.controller.execute(ACTION.SHOW_PICTURE_BALLOON_POPUP, focus.pictureId);
            } else if (!this.selection.isCollapsed() && this.selection.isTextSelection()) {
                this.controller.execute(ACTION.SHOW_TEXT_BALLOON_POPUP);
            }
        }
        postExecute() {}
    },

    [ACTION.CLEAR_SELECTION]: class extends SelectionController {
        execute() {
            this.selectionRenderer.empty();
            this.selectionRenderer.stop();
        }
        postExecute() {}
    }
};

export default selectionController;


