import TextRun from './TextRun';

class Tab extends TextRun {
    /**
     * TextRun tab  .
     * @returns {TextRun} textRun Tab
     */
    static createTab() {
        return new Tab({tab: 1});
    }

    /**
     * Tab length .
     * @returns {Number}
     */
    length() {
        return 1;
    }

    /**
     *  TextRun  index  Tab    TextRun .
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default Tab;


