import $ from 'jquery';
import util from '../util/util';

/**
 *     Table  Class.<br/>
 * ParagraphRenderer TableRenderer .
 * @example
 * import TableRenderer from 'this file path';
 * let tableRenderer = TableRenderer.getInstance();
 */
class TableRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    /**
     *    .
     * @returns {Object} -  
     */
    getEditor() {
        return this.parent.editor;
    }

    /**
     *    Table Element   HTML .
     * @param {Table} - Table Element
     * @returns {String} - HTML  
     */
    getHTML(table) {
        if (util.isNil(table)) {
            return '';
        }
        let $elTable = $(`<table table-id='${table.getId()}'></table>`);
        let $elTableBody = $('<tbody></tbody>');
        let tableRowRenderer = this.parent.getTableRowRenderer();

        table.getChildren().forEach(tableRow => {
            let html = tableRowRenderer.getHTML(tableRow);
            $elTableBody.append(html);
        });

        //width
        if (table.getWidth() < 0) {
            $elTable.addClass('auto');
        }

        $elTable.append($elTableBody);

        return $elTable.prop('outerHTML');
    }
}

export default TableRenderer;


