import HTMLUtil from '../../util/HTMLUtil';
import consts from '../../consts/consts';

const {POSITION_TYPE} = consts;

/**
 *    Class.<br/>
 *         Element  API .
 * @example
 * import Position from 'path';
 * var position = new Position(2, 10);
 */
class Position {
    /**
     * id offset  .
     * @param {String} id element id
     * @param {Number} [offset = 0]   ex)'|'   2
     */
    constructor(id, offset = 0) {
        this.id = parseInt(id);
        this.offset = offset;
    }

    /**
     *  type .
     * @returns {String}
     */
    getType() {
        return POSITION_TYPE.TEXT;
    }

    /**
     * position   .
     * @returns {Position} Clone Position
     */
    clone(){
        return new Position(this.id, this.offset);
    }

    /**
     * id, offset   Position .
     * @param {Position} position
     * @returns {Boolean}
     */
    isEqual(position) {
        return this.getType() === position.getType() && this.id === position.id && this.offset === position.offset;
    }

    /**
     * Position Rect .
     * @returns {DOMRect} A [DOMRect]{@link https://developer.mozilla.org/en-US/docs/Web/API/DOMRect} object that bounds the contents of the range.
     */
    getRect(editorId) {
        let domElement = HTMLUtil.getHTMLElement(editorId, this.id);
        let runLevelHTMLElement = HTMLUtil.getRunLevelHTMLElement(domElement, this.offset);
        let offsetGap = 0;
        let rect, left;

        // <br> + <span>     
        if (HTMLUtil.isBrHTMLElement(runLevelHTMLElement) && this.offset > 0) {
            let element = runLevelHTMLElement;
            while(element = element.nextElementSibling) {
                if (HTMLUtil.isSpanHTMLElement(element) || HTMLUtil.getHTMLSpanLength(element) > 0) {
                    runLevelHTMLElement = element;
                    break;
                } else {
                    offsetGap += HTMLUtil.getHTMLSpanLength(element);
                }
            }
        }
        let textNode = runLevelHTMLElement.firstChild;

        if (HTMLUtil.isSpanHTMLElement(runLevelHTMLElement) && !HTMLUtil.isEmptyTextNode(textNode)) {
            let runOffset = this.offset + offsetGap - HTMLUtil.getHTMLSpanOffset(runLevelHTMLElement);
            let range = document.createRange();
            let isLastOffset = textNode.length === runOffset;

            // EDITOR-538 safari range collapse, getBoundingClientRect  0 
            range.setStart(textNode, runOffset + (isLastOffset ? -1 : 0));
            range.setEnd(textNode, runOffset + (isLastOffset ? 0 : 1));
            rect = range.getBoundingClientRect();
            left = isLastOffset ? rect.right : rect.left;
        } else {
            rect = runLevelHTMLElement.getBoundingClientRect();
            let runOffset = this.offset;
            if (HTMLUtil.isImageHTMLElement(runLevelHTMLElement)) {
                runOffset -= HTMLUtil.getHTMLSpanOffset(runLevelHTMLElement);
            }
            left = runOffset === 0 ? rect.left : rect.right;
        }

        let {top, bottom, height} = rect;

        return {
            x: left,
            y: top,
            width: 0,
            height: height,
            left: left,
            right: left,
            top: top,
            bottom: bottom
        };
    }
}

export default Position;


