import TextRun from './TextRun';

/**
 * HyperlinkEnd
 * @extends TextRun
 */
class HyperlinkEnd extends TextRun {
    /**
     * HyperlinkEnd  .
     * @param {Number} id
     * @return {HyperlinkEnd}
     */
    static createHyperlinkEnd(id) {
        return new HyperlinkEnd({
            id: id
        });
    }

    /**
     * HyperlinkEnd length .
     * @returns {Number}
     */
    length() {
        return 0;
    }

    /**
     * Element Type .
     * @returns {String} HyperlinkEnd
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'HyperlinkEnd';
    }

    /**
     *  TextRun  index  HyperlinkEnd    TextRun .
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }
}

export default HyperlinkEnd;


