import consts from '../consts/consts';

const {POSITION_TYPE} = consts;

/**
 *    class . </br>
 *   ,   API .
 */
class CellIndex {
    /**
     * startRowIndex, startColIndex, endRowIndex, endColIndex  .
     * @param {Number} startRowIndex 
     * @param {Number} startColIndex 
     * @param {Number} [endRowIndex = startRowIndex]
     * @param {Number} [endColIndex = startColIndex]
     */
    constructor(startRowIndex, startColIndex, endRowIndex = startRowIndex, endColIndex = startColIndex) {
        this.sr = startRowIndex;
        this.er = endRowIndex;
        this.sc = startColIndex;
        this.ec = endColIndex;
    }

    /**
     *   cellIndex  .
     * @param {CellPosition} anchor 
     * @param {CellPosition} focus 
     * @returns {CellIndex}
     */
    static getCellIndexFromPosition(anchor, focus) {
        if ([anchor, focus].some(pos => pos.getType() !== POSITION_TYPE.CELL)) {
            return null;
        }
        let [sr, er] = [anchor.rowIndex, focus.rowIndex].sort((a, b) => a - b);
        let [sc, ec] = [anchor.colIndex, focus.colIndex].sort((a, b) => a - b);
        return new CellIndex(sr, sc, er, ec);
    }

    /**
     *   cellIndex    .
     * @param {CellIndex} cellIndex 
     * @returns {Boolean}
     */
    isOut(cellIndex) {
        if (!cellIndex) {
            return false;
        } else {
            return this.sr > cellIndex.sr ||
                   this.er < cellIndex.er ||
                   this.sc > cellIndex.sc ||
                   this.ec < cellIndex.ec;
        }
    }

    /**
     *   cellIndex      .
     * @param {CellIndex} cellIndex 
     * @returns {CellIndex}
     */
    mergeIndex(cellIndex) {
        this.sr = Math.min(this.sr, cellIndex.sr);
        this.er = Math.max(this.er, cellIndex.er);
        this.sc = Math.min(this.sc, cellIndex.sc);
        this.ec = Math.max(this.ec, cellIndex.ec);
        return this;
    }

    /**
     * start row, start col  cellIndex .
     * @returns {CellIndex}
     */
    getStartCellIndex() {
        return new CellIndex(this.sr, this.sc);
    }

    /**
     * end row, end col  cellIndex .
     * @returns {CellIndex}
     */
    getEndCellIndex() {
        return new CellIndex(this.er, this.ec);
    }
}

export default CellIndex;


