import Position from './Position';
import consts from '../../consts/consts';
import CellIndex from '../CellIndex';

const {POSITION_TYPE} = consts;

/**
 *     class.<br/>
 *         Element  API .
 * @example
 * import CellPosition from 'path';
 * var cellPosition = new CellPosition(1, 2, 2);
 */
class CellPosition extends Position {
    /**
     * id, offset, rowIndex, colIndex  .
     * @param {String} id table element id
     * @param {Number} offset  
     * @param {Number} rowIndex  
     * @param {Number} colIndex  
     */
    constructor(id, offset, tableId, rowIndex, colIndex) {
        super(id, offset);
        this.tableId = tableId;
        this.rowIndex = rowIndex;
        this.colIndex = colIndex;
    }

    /**
     *  type .
     * @returns {String}
     */
    getType() {
        return POSITION_TYPE.CELL;
    }

    /**
     * CellPosition CellIndex .
     * @returns {CellIndex}
     */
    getCellIndex() {
        return new CellIndex(this.rowIndex, this.colIndex);
    }

    /**
     * cell position   .
     * @returns {CellPosition} Clone CellPosition
     */
    clone() {
        return new CellPosition(this.id, this.offset, this.tableId, this.rowIndex, this.colIndex);
    }

    /**
     * id, offset, tableId, rowIndex, colIndex   CellPosition .
     * @param {Position} position
     * @returns {Boolean}
     */
    isEqual(position) {
        return super.isEqual(position) && 
               this.tableId === position.tableId && 
               this.rowIndex === position.rowIndex && 
               this.colIndex === position.colIndex;
    }
}

export default CellPosition;


