import DrawingObject from './DrawingObject';
/**
 * HorizontalLine
 * @extends Element
 */
class HorizontalLine extends DrawingObject {
    /**
     * new HorizontalLine  Element  __default__()  HorizontalLine      .
     * @private
     * @returns {Object}  HorizontalLine  Object
     */
    __default__() {
        return {
            prop: {
                color: {
                    type: 'SRGB',
                    srgb_color: {
                        value_in_hex: 0
                    }
                },
                height_in_sunit: 1500,
                width: {
                    type: 'percent',
                    value_in_percent: 100000
                }
            }
        };
    }
    /**
     * Element Type .
     * @returns {String} Picture
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'HorizontalLine';
    }
}

export default HorizontalLine;


