import uiController from './uiController';
import textController from './textController';
import paragraphController from './paragraphController';
import selectionController from './selectionController';
import pictureController from './pictureController';
import clipboardController from './clipboardController';
import undoRedoController from './undoRedoController';
import tableController from './tableController';

/**
 *    Class .<br/>
 * , ,     excute method  .
 */
class Controller {
    constructor (editor) {
        this.editor = editor;
        this.classes = Object.assign({}, uiController, textController, paragraphController, selectionController, pictureController, clipboardController, undoRedoController, tableController);
    }

    /**
     * Controller  ControllerClass   . <br/>
     * controller   . <br/>
     * @param {String} name action name
     * @param {Array} [args] execute   
     * @example example 
     */
    execute (name, ...args) {
        let controllerClass = this.classes[name];
        if (controllerClass && controllerClass.isControllerClass) {
            let controllerInstance = new controllerClass(this.editor, name);
            controllerInstance.preExecute();
            controllerInstance.execute(...args);
            controllerInstance.postExecute();
        } else {
            throw new Error('Don\'t have Action');
        }
    }
}

export default Controller;


