/**
 *   interface  class.<br/>
 */
class ControllerInterface {
    /**
     * Controller class    .
     */
    static isControllerClass() {
        return true;
    }

    /**
     *     .
     */
    preExecute() {}
    
    /**
     *   .
     * @param {arguments} [args] execute   
     */
    execute(...args) {}
    
    /**
     *     .
     */
    postExecute() {}
}

export default ControllerInterface;


