import $ from 'jquery';
import util from '../util/util';

/**
 *     TableRow  Class.<br/>
 * TableRenderer TableRowRenderer .
 */
class TableRowRenderer {
    constructor(renderer) {
        this.parent = renderer;
    }

    /**
     *    .
     * @returns {Object} -  
     */
    getEditor() {
        return this.parent.editor;
    }

    /**
     *     TableRow Element   HTML .
     * @param {TableRow} tableRow TableRow Element
     * @returns {String} HTML  
     */
    getHTML(tableRow) {
        if (util.isNil(tableRow)) {
            return '';
        }
        
        let $elTableRow = $(`<tr></tr>`);
        let tableCellRenderer = this.parent.getTableCellRenderer();
        
        tableRow.getChildren().forEach(tableCell => {
            let html = tableCellRenderer.getHTML(tableCell);
            $elTableRow.append(html);
        });

        //height
        let height = tableRow.getHeight();
        if (height > 0) {
            $elTableRow.attr('height', `${height}px`);
        }

        return $elTableRow.prop('outerHTML');
    }
}

export default TableRowRenderer;


