import util from '../../util/util';
import consts from '../../consts/consts';

/*
 *    (3 )
 * bullet: [, , ]
 * numbering: [1, 1, 1]
 */
const LISTS_DEFAULT_FORMAT = {
    BULLET: [consts.LISTS_LEVEL_TEXT.DISC, consts.LISTS_LEVEL_TEXT.CIRCLE, consts.LISTS_LEVEL_TEXT.SQUARE],
    NUMBERING: [consts.LISTS_FORMAT.DECIMAL, consts.LISTS_FORMAT.DECIMAL, consts.LISTS_FORMAT.DECIMAL]
};

//   
const LISTS_DEFAULT_NUMBERING_TEXTRUN_PROP = {
    [consts.LISTS_LEVEL_TEXT.DISC]: {
        hint: "_default",
        ascii: "Arial",
        high_ansi: "Arial"
    },
    [consts.LISTS_LEVEL_TEXT.CIRCLE]: {
        hint: "_default",
        ascii: "Arial",
        high_ansi: "Arial",
        complex_script: "Arial"
    },
    [consts.LISTS_LEVEL_TEXT.SQUARE]: {
        hint: "_default",
        ascii: "Arial",
        high_ansi: "Arial"
    }
};

const LISTS_MULTI_LEVEL_TYPE = consts.LISTS_MULTI_LEVEL_TYPE;
const LISTS_FORMAT = consts.LISTS_FORMAT;
const LISTS_TYPE = consts.LISTS_TYPE;
const LISTS_INDENT_STEP = consts.LISTS_INDENT_STEP;
const HORIZONTAL_ALIGNMENT = consts.HORIZONTAL_ALIGNMENT;

/**
 * tagList  HTML Element Model  .<br/>
 *  docModel JSON   .
 */
class HTMLToModel {
    constructor() {
        this.tagList = {
            'P': 'PARAGRAPH',
            'LI': 'PARAGRAPH',
            'H1': 'HEADING',
            'H2': 'HEADING',
            'H3': 'HEADING',
            'H4': 'HEADING',
            'H5': 'HEADING',
            'H6': 'HEADING',
            'UL': 'LIST',
            'SPAN': 'TEXTRUN',
            'A': 'HYPERLINK',
            'BR': 'BREAK',
            'HR': 'HR',
            'IMG': 'PICTURE',
            'TABLE': 'TABLE',
            'TBODY': 'TBODY',
            'TR': 'TABLEROW'
        };
        this.__init__();
    }

    /**
     * Model     .
     * @private
     */
    __init__() {
        // cssRules: css 
        // isLists:    
        // isHyperLinkEnd:    
        // numbering: numbering .
        // numberingLevel:  numbering   numberingLevel .
        // numberingToRevise:   numbering .
        // tableInfoStack:    .  > [{table: tableHTMLElement, rowspans: { tr : [{idx: td, colspan: Number|undefined}, ...] }}, {}...]
        // rIdSeq : relationship Id Sequence
        // relationship :  . [{relationship}, {relationship}, ...]
        this.cssRules = undefined;
        this.isLists = false;
        this.isHyperLinkEnd = true;
        this.numbering = [];
        this.numberingLevel = [];
        this.numberingToRevise = [];
        this.tableInfoStack = [];
        this.rIdSeq = 0;
        this.relationship = [];
    }

    /**
     *       .
     * @private
     * @param {HTMLElement} tableElement TABLE HTML Element
     */
    __setTableInfo__(tableElement) {
        const tableInfo = {
            table: tableElement,
            rowspans: {} // rowspan  td  
        };
        this.tableInfoStack.push(tableInfo);
    }

    /**
     *      .
     * @private
     * @returns {Object|undefined}  
     */
    __getTableInfo__() {
        const idx = this.tableInfoStack.length - 1;
        return this.tableInfoStack[idx];
    }

    /** 
     *       .
     * @private
     */
    __removeTableInfo__() {
        this.tableInfoStack.pop();
    }

    /**
     * HTML Element     Model   .
     * @private
     * @param {HTMLElement} parentElement 
     * @returns {Array} Model
     */
    __getParagraphChildren__(parentElement) {
        if(!!parentElement.firstChild && this.tagList[parentElement.firstChild.tagName] === 'HEADING') {
            parentElement = parentElement.firstChild;
        }
        let childrenElement = [];
        Array.from(parentElement.childNodes).forEach(child => {
            switch (this.tagList[child.nodeName]) {
                case 'TEXTRUN':
                    childrenElement.push(this.convertToTextRun(child));
                    break;
                case 'HYPERLINK':
                    childrenElement.push(...this.__getHyperLinkRange__(child));
                    break;
                case 'BREAK':
                    childrenElement.push(this.__getBreakTextRun__());
                    break;
                case 'PICTURE':
                    childrenElement.push(this.convertToDrawingObj(child));
                    break;
            }
        });
        return childrenElement;
    }

    /**
     *   (LI) Paragraph   .
     * @private
     * @param {HTMLElement} listElement  Element (UL)
     * @param {Number} numberingId  ID
     * @returns {Array} Paragraphs
     */
    __getListParagraphs__(listElement, numberingId) {
        const paragraphs = [];

        Array.from(listElement.childNodes).forEach(child => {
            switch (this.tagList[child.nodeName]) {
                case 'PARAGRAPH':
                    paragraphs.push(this.convertToParagraph(child, numberingId));
                    break;
            }
        });

        return paragraphs;
    }

    /**
     *     .<br/>
     *        .
     * @private
     * @param {HTMLElement} listElement LIST HTML Element (UL/OL)
     * @returns {Object}  
     * @example
     * //  
     * numberingId:  ID (Number)
     * multiLevelType:   (String)
     */
    __getListProp__(listElement) {
        const listProp = {};

        if (!!listElement.getAttribute('lists-id')) {
            listProp.numberingId = parseInt(listElement.getAttribute('lists-id'));
        }

        if (!!listElement.getAttribute('type')) {
            listProp.multiLevelType = listElement.getAttribute('type');
        }

        return listProp;
    }

    /**
     * Paragraph    .<br/>
     * Paragraph       .
     * @private
     * @param {HTMLElement} paragraphElement Paragraph HTML Element
     * @returns {Object} Paragraph 
     * @example
     * //  
     * textAlign:  (String)
     * lineHeight:  (Number)
     * paddingLeft:  (Number)
     * paraStyle:  (String) H1, ..., H6
     */
    __getParagraphProp__(paragraphElement) {
        const paragraphProp = {};
        const cssStyle = paragraphElement.style;
        const tagName = paragraphElement.tagName;
        const childTagName = paragraphElement.firstChild && paragraphElement.firstChild.tagName;

        if (!!cssStyle.textAlign) { // 
            paragraphProp.textAlign = cssStyle.textAlign;
        }

        if (!!cssStyle.lineHeight) { // 
            paragraphProp.lineHeight = Math.round(util.cssStringToFloat(cssStyle.lineHeight) * 100000);
        }

        if (!!cssStyle.paddingLeft) { // 
            paragraphProp.paddingLeft = util.px2sunit(parseInt(cssStyle.paddingLeft));
        }

        if (!!tagName && this.tagList[tagName] === 'HEADING') { // para_style  taglist h  h1~h6 .)
            paragraphProp.paraStyle = tagName;
        }

        if (!!childTagName && this.tagList[childTagName] === 'HEADING') { // LI  h1 ~ h6  .
            paragraphProp.paraStyle = childTagName;
        }

        return paragraphProp;
    }

    /**
     * HTML  .
     * @private
     * @param {HTMLElement} element   HTML Element
     * @returns {String} HTML 
     */
    __getTextContent__(element) {
        if (element.textContent === consts.ZERO_WIDTH_SPACE) {
            return ''; // &#8203;  
        }

        return element.textContent;
    }

    /**
     * element css      . <br/>
     *  prop       .
     * @private
     * @param {HTMLElement} element 
     * @param {HTMLElement} [rootElement]    Element
     * @returns {Object} textrun prop
     * @example
     * //  
     * fontFamily :  (String)
     * fontSize :   (Number - decimal)
     * italic :  (Boolean)
     * bold :  (Boolean)
     * underline :  (Boolean)
     * strike :  (Boolean)
     * color :   (Number - decimal)
     * backgroundColor :   (Number - decimal)
     * tab :   (Boolean)
     */
    __getTextRunProp__(element, rootElement) {
        const cssStyle = element.style;
        const textRunProp = {};

        if (!!cssStyle.fontFamily) {
            textRunProp.fontFamily = cssStyle.fontFamily;
        }

        if (!!cssStyle.fontSize) {
            textRunProp.fontSize = util.cssStringToPt(cssStyle.fontSize) * 1000;
        }

        if (cssStyle.fontStyle === 'italic') {
            textRunProp.italic = true;
        }

        if (!!cssStyle.fontWeight) {
            // fontWeight 'bold'  'bolder' 600  bold 
            const isBold = cssStyle.fontWeight === 'bold' ||
                cssStyle.fontWeight === 'bolder' ||
                parseInt(cssStyle.fontWeight) >= 600;
            textRunProp.bold = isBold;
        }

        if (cssStyle.textDecoration.indexOf('underline') > -1) {
            textRunProp.underline = true;
        }

        if (cssStyle.textDecoration.indexOf('line-through') > -1) {
            textRunProp.strike = true;
        }

        if (!!cssStyle.color) {
            textRunProp.color = util.rgbStringToDecimal(cssStyle.color);
        }

        if (!!cssStyle.backgroundColor) {
            textRunProp.backgroundColor = util.rgbStringToDecimal(cssStyle.backgroundColor);
        }

        if (!!cssStyle.marginLeft) { // tab
            textRunProp.tab = true;
        }

        return textRunProp;
    }

    /**
     * Break TextRun 
     * @private
     * @returns {Object} Break TextRun
     */
    __getBreakTextRun__() {
        return {
            type: 'TEXTRUN',
            textrun: {
                break: {
                    clear: 'none',
                    type: 'line'
                }
            }
        };
    }

    /**
     * tab  TextRun 
     * @private
     * @returns {Object} Tab TextRun
     */
    __getTabTextRun__() {
        return {
            type: 'TEXTRUN',
            textrun: {
                tab: 1
            }
        };
    }

    /**
     * A Element   hyperlink  . <br/>
     * A Tag nextSibling  ,   paragraph   A Tag, id  true .
     * @private
     * @param {HTMLElement} hyperlink
     * @param {Number} id
     * @returns {Boolean}
     */
    __checkHyperLinkEnd__(hyperlink, id) {
        const para = hyperlink.parentElement;
        const nextPara = para.nextSibling;
        if (!hyperlink.nextSibling) {
            if(nextPara && nextPara.firstChild) {
                const nextParaFirstChild = nextPara.firstChild;
                return nextParaFirstChild.tagName === 'A' && parseInt(nextParaFirstChild.getAttribute('hyperlink-id')) === id ? false : true;
            }
        }
        return true;
    }

    /**
     * A Element   HyperLinkRange   . <br/>
     * hyperlink    isHyperLinkRange Flag  hyperLinkRange  .
     * @private
     * @param {HTMLElement} hyperlink
     * @returns {Array} HyperLinkRange
     */
    __getHyperLinkRange__(hyperLink) {
        let hyperlinkRange = [];
        let id = parseInt(hyperLink.getAttribute('hyperlink-id'));
        let href = hyperLink.href;

        if (this.isHyperLinkEnd) {
            hyperlinkRange.push(this.__getHyperLinkStart__(id, href));
        }        
        Array.from(hyperLink.childNodes).forEach(child => {
            switch (this.tagList[child.nodeName]) {
                case 'TEXTRUN':
                    hyperlinkRange.push(this.convertToTextRun(child));
                    break;
            }
        });
        if(this.__checkHyperLinkEnd__(hyperLink, id)) {
            hyperlinkRange.push(this.__getHyperLinkEnd__(id));
            this.isHyperLinkEnd = true;
        } else {
            this.isHyperLinkEnd = false;
        }
        return hyperlinkRange;
    }

    /**
     * HyperLinkStart TextRun 
     * @private
     * @param {Number} id
     * @param {String} href
     * @returns {Object} HyperLinkStart TextRun
     */
    __getHyperLinkStart__(id, href) {
        return {
            type: "HYPERLINK_START",
            hyperlink_start: {
                id: id,
                prop: {
                    target: href,
                    type: "EXTERNAL"
                }
            }
        };
    }

    /**
     * HyperLinkEnd TextRun 
     * @private
     * @param {Number} id
     * @returns {Object} HyperLinkEnd TextRun
     */
    __getHyperLinkEnd__(id) {
        return {
            type: "HYPERLINK_END",
            hyperlink_end: {
                id: id
            }
        };
    }

    /**
     * IMG Element    .
     * @private
     * @param {HTMLElement} imgElement IMG Element
     * @returns {Object}
     */
    __getPictureProp__(imgElement) {
        const pictureProp = {};
        const cssStyle = imgElement.style;

        if (!!cssStyle.width) {
            pictureProp.width = util.px2emu(parseInt(cssStyle.width));
        }

        if (!!cssStyle.height) {
            pictureProp.height = util.px2emu(parseInt(cssStyle.height));
        }

        return pictureProp;
    }

    /**
     * Paragraph end_run_prop  . <br/>
     *    prop  .
     * @private
     * @param {Array} children 
     * @returns {JSONObject} run prop
     */
    __getEndrunProp__(children) {
        if (children.length > 0) {
            return util.get(children, [children.length - 1, 'textrun', 'prop']);
        }
    }

    /**
     * className , class  numberingLevel .
     * @private
     * @param {String} className 
     * @returns {Number} numberingLevel
     */
    __getLevelInClassName__(className) {
        let numberingLevel = 0;
        className.split(' ').forEach(val => {
            if (val.indexOf('level_') > -1) {
                numberingLevel = val.split('_')[1];
            }
        });
        return parseInt(numberingLevel);
    }

    /**
     * ParagraphElement   numberinglist    levelList  .<br/>
     * levelList    HTML attribute  .<br/>
     * format bullet  textrun_prop .
     * @private
     * @param {HTMLElement} paragraphElement
     * @returns {Object}  
     */
    __getNumberingLevel__(paragraphElement) {
        const numberingProp = this.__getNumberingLevelProp__(paragraphElement);
        const length = this.numberingLevel.length;
        let revise = false;

        // numberingProp level   numbering level revise Flag true .
        if (this.numberingToRevise.indexOf(numberingProp.level) > -1) {
            revise = true;
        }
        //   level   return.
        if (!revise && length >= 1 && this.numberingLevel[length - 1].level >= numberingProp.level) {
            return this.numberingLevel[numberingProp.level];
        }

        const isLowerRoman = numberingProp.format === LISTS_FORMAT.LOWER_ROMAN;
        const isBullet = numberingProp.format === LISTS_TYPE.BULLET;

        const numberingLevel = {
            level: numberingProp.level,
            start_value: numberingProp.startValue,
            format: numberingProp.format,
            level_text: numberingProp.levelText,
            align: isLowerRoman ? HORIZONTAL_ALIGNMENT.RIGHT : HORIZONTAL_ALIGNMENT.LEFT,
            para_prop: {
                indent: {
                    left_in_sunit: LISTS_INDENT_STEP * (numberingProp.level + 1),
                    first_type: 'out',
                    first_in_sunit: isLowerRoman ? 9000 : 18000
                }
            }
        };

        if (isBullet) {
            numberingLevel.textrun_prop = {
                fonts: this.__getBulletFontProp__(paragraphElement) || LISTS_DEFAULT_NUMBERING_TEXTRUN_PROP[numberingProp.levelText]
            };
        }

        if (revise) {
            this.numberingLevel[numberingProp.level] = numberingLevel;
        } else {
            //   numbringProp level  level numberingLevel ,  numberingProp 
            //   numbering level  reviseNumberingList .
            let type = paragraphElement.parentElement.type;
            let clonedNumberingLevel, levelText, format;
            for (var i = 0; i < numberingProp.level; i++) {
                clonedNumberingLevel = util.cloneDeep(numberingLevel);
                levelText = clonedNumberingLevel.level_text;
                format = clonedNumberingLevel.format;

                if (!this.numberingLevel[i]) {
                    clonedNumberingLevel.level = i;
                    clonedNumberingLevel.level_text = this.__getRevisedLevelText__(levelText, type, format, i);
                    clonedNumberingLevel.para_prop.indent.left_in_sunit = LISTS_INDENT_STEP * (i + 1);
                    this.numberingToRevise.push(i);
                    this.numberingLevel[i] = clonedNumberingLevel;
                }
            }
            this.numberingLevel.push(numberingLevel);
        }
        return numberingLevel;
    }

    /**
     * decimal foramt levelText   .
     * @private
     * @param {String} levelText numberingLevel levelText
     * @param {String} type  multiLevel || hybridMultiLevel
     * @param {String} format bullet || decimal
     * @param {Number} index   numberingLevel index
     * @returns {String} levelText
     */
    __getRevisedLevelText__(levelText, type, format, index) {
        if(format === 'decimal') {
            if (type === 'multilevel') { // 
                levelText = levelText.slice(0, 3 + (3 * index));
            } else {
                levelText = levelText.replace(/\d+/, index + 1);
            }
        } 
        return levelText;
    }

    /**
     * css      font prop  .
     * @private
     * @param liElement
     * @return {Object|undefined} font prop
     */
    __getBulletFontProp__(liElement) {
        if (!this.cssRules || !liElement || !liElement.id) {
            return;
        }

        let font;
        const selectorText = `#${liElement.id}::before`;
        for (let i = 0, len = this.cssRules.length; i < len; i++) {
            const cssRule = this.cssRules.item(i);

            if (cssRule.selectorText === selectorText) {
                font = cssRule.style.fontFamily;
                break;
            }
        }

        if (!!font) {
            return {
                hint: "_default",
                ascii: font,
                high_ansi: font
            };
        }
    }

    /**
     *      .<br/>
     *         .
     * @private
     * @param {HTMLElement} paragraphElement Paragraph HTML Element
     * @returns {Object}     
     * @example
     * //  
     * level:  paragraph  (Number)
     * startValue:    (Number)
     * format: 'bullet'  'decimal' (String)
     * levelText: ( )   (String)
     */
    __getNumberingLevelProp__(paragraphElement) {
        const numberingProp = {};

        if (!!paragraphElement.className) {
            numberingProp.level = this.__getLevelInClassName__(paragraphElement.className);
        }

        if (!!paragraphElement.getAttribute('startval')) {
            numberingProp.startValue = parseInt(paragraphElement.getAttribute('startval'));
        }

        if (!!paragraphElement.getAttribute('format')) {
            numberingProp.format = paragraphElement.getAttribute('format');
        }

        if (!!paragraphElement.getAttribute('text')) {
            numberingProp.levelText = paragraphElement.getAttribute('text');
        }

        return numberingProp;
    }

    /**
     *  9   numberingLevel .
     * @private
     */
    __addNumberingLevel__() {
        let len = this.numberingLevel.length;
        let firstLevel = this.numberingLevel[0];
        let currNumbering = this.numbering[this.numbering.length - 1];
        let type = currNumbering.multi_level_type;
        let startValue = firstLevel.start_value;
        let levelText = firstLevel.level_text;
        let isBullet = type === LISTS_MULTI_LEVEL_TYPE.HYBRID_MULTI_LEVEL && firstLevel.format === LISTS_TYPE.BULLET;
        let formatList = isBullet ? LISTS_DEFAULT_FORMAT.BULLET : LISTS_DEFAULT_FORMAT.NUMBERING;

        for (let i = len; i < 9; i++) {
            let numberingLevel = {
                level: i,
                start_value: startValue,
                format: isBullet ? LISTS_TYPE.BULLET : formatList[i % 3],
                level_text: isBullet ? formatList[i % 3] : levelText.replace(/\d+/, i + 1),
                align: isBullet ? HORIZONTAL_ALIGNMENT.LEFT : formatList[i % 3] === LISTS_FORMAT.LOWER_ROMAN ? HORIZONTAL_ALIGNMENT.RIGHT : HORIZONTAL_ALIGNMENT.LEFT,
                para_prop: {
                    indent: {
                        left_in_sunit: LISTS_INDENT_STEP * (i + 1),
                        first_type: 'out',
                        first_in_sunit: formatList[i % 3] === LISTS_FORMAT.LOWER_ROMAN ? 9000 : 18000
                    }
                }
            };

            if (isBullet) {
                numberingLevel.textrun_prop = {
                    fonts: LISTS_DEFAULT_NUMBERING_TEXTRUN_PROP[numberingLevel.level_text]
                };
            }

            this.numberingLevel.push(numberingLevel);
        }
    }

    /**
     * style      . <br/>
     *  prop       .
     * @private
     * @param {HTMLElement} tableElement TABLE Element
     * @returns {Object} table prop
     * @example
     * //  
     * isAutoWidth:     (Boolean)
     */
    __getTableProp__(tableElement) {
        const tableProp = {};

        if (tableElement.className.indexOf('auto') >= 0) {
            tableProp.isAutoWidth = true;
        }

        return tableProp;
    }

    /**
     * style      . <br/>
     *  prop       .
     * @private
     * @param {HTMLElement} trElement TR Element
     * @returns {Object} tableRow prop
     * @example
     * //  
     * height:    (Number - sunit)
     */
    __getTableRowProp__(trElement) {
        const tableRowProp = {};
        const cssStyle = trElement.style;

        const height = trElement.getAttribute('height') || cssStyle.height;
        if (!!height) {
            tableRowProp.height = util.px2sunit(parseFloat(height));
        }

        return tableRowProp;
    }

    /**
     * style      . <br/>
     *  prop       .
     * @private
     * @param {HTMLElement} tdElement TD Element
     * @returns {Object} tableCell prop
     * @example
     * //  
     * colspan :  (Number)
     * rowspan :  (Number)
     * width :  (Number - sunit)
     * backgroundColor :   (Number - hexdecimal)
     * borderTop :   (Object {style: String, color: Number - hexdecimal, width: Number - sunit}) 
     * borderLeft :   (Object {style: String, color: Number - hexdecimal, width: Number - sunit}) 
     * borderRight :   (Object {style: String, color: Number - hexdecimal, width: Number - sunit}) 
     * borderBottom :   (Object {style: String, color: Number - hexdecimal, width: Number - sunit})
     */
    __getTableCellProp__(tdElement) {
        const cssStyle = tdElement.style;
        const tableCellProp = {};

        if (!!tdElement.getAttribute('colspan')) {
            tableCellProp.colspan = parseInt(tdElement.getAttribute('colspan'));
        }

        if (!!tdElement.getAttribute('rowspan')) {
            tableCellProp.rowspan = parseInt(tdElement.getAttribute('rowspan'));
        }

        const width = tdElement.getAttribute('width') || cssStyle.width;
        if (!!width) {
            const widthUnit = util.getUnitFromString(width);

            if (widthUnit === consts.CSS_UNIT.PERCENT) {
                tableCellProp.widthType = 'pct';
                tableCellProp.width = util.cssStringToFloat(width) * 100000;
            } else {
                tableCellProp.widthType = 'sunit';
                tableCellProp.width = util.px2sunit(util.cssStringToFloat(width));
            }
        }

        if (!!cssStyle.backgroundColor) {
            tableCellProp.backgroundColor = util.rgbStringToDecimal(cssStyle.backgroundColor);
        }

        // border prop
        ['Top', 'Bottom', 'Left', 'Right'].forEach((positionStr) => {
            const styleStr = `border${positionStr}Style`;
            const colorStr = `border${positionStr}Color`;
            const widthStr = `border${positionStr}Width`;

            if (!cssStyle[styleStr] && !cssStyle[colorStr] && !cssStyle[widthStr]) {
                return;
            }

            const borderProp = tableCellProp[`border${positionStr}`] = {};

            if (!!cssStyle[styleStr]) { //  
                borderProp.style = cssStyle[styleStr] === 'solid' ? 'single' : cssStyle[styleStr];
            }

            if (!!cssStyle[colorStr]) { //  
                borderProp.color = util.rgbStringToDecimal(cssStyle[colorStr]);
            }

            if (!!cssStyle[widthStr]) { //  
                borderProp.width = util.px2sunit(parseFloat(cssStyle[widthStr]));
            }
        });

        return tableCellProp;
    }

    /**
     *  tableRow 
     * @private
     * @param {HTMLElement} tableElement  HTML Element
     * @returns {Array} table row 
     */
    __getTableRows__(tableElement) {
        const tableRows = [];

        Array.from(tableElement.childNodes).forEach((child) => {
            switch (this.tagList[child.nodeName]) {
                case 'TBODY':
                    // tbody  tbody  Element  
                    Array.from(child.childNodes).forEach((tbodyChild) => {
                        switch (this.tagList[tbodyChild.nodeName]) {
                            case 'TABLEROW':
                                tableRows.push(this.convertToTableRow(tbodyChild));
                                break;
                        }
                    });
                    break;
            }
        });

        return tableRows;
    }

    /**
     * Table Cell Paragraph  .
     * @private
     * @param {HTMLElement} tdElement TD
     * @returns {Array} Paragraphs
     */
    __getTableCellParagraphs__(tdElement) {
        const paragraphs = [];

        if (tdElement.childNodes.length === 0) {
            //  node   Paragraph 
            tdElement.appendChild(this.__createEmptyParaElement__());
        }

        Array.from(tdElement.childNodes).forEach((child) => {
            const para = this.__getTableCellParagraphObject__(child);

            if (!!para) {
                if (Array.isArray(para)) {
                    paragraphs.push(...para);
                } else {
                    paragraphs.push(para);
                }
            }
        });

        return paragraphs;
    }

    /**
     *   Element  Model   .<br/>
     *     undefined .
     * @private
     * @param {HTMLElement} element   element
     * @returns {Object|Array|undefined} element Model      
     */
    __getTableCellParagraphObject__(element) {
        let paragraphObject;

        switch (this.tagList[element.nodeName]) {
            case 'PARAGRAPH':
            case 'HEADING':
                paragraphObject = this.convertToParagraph(element).paragraph;
                break;
            case 'LIST':
                if(!this.__checkEmptyChildren__(element)) {
                    paragraphObject = this.convertToList(element).map((para) => {
                        return para.paragraph;
                    });
                }
                break;
            case 'HR':
            case 'TABLE':
                paragraphObject = this.convertToDrawingObj(element).paragraph;
                break;
        }

        return paragraphObject;
    }

    /**
     * TR Element  TD  TableCell Model  
     * @private
     * @param {HTMLElement} trElement TR HTML Element
     * @returns {Array} tableCell Model
     */
    __getTableCells__(trElement) {
        const tableCells = [];
        const tableInfo = this.__getTableInfo__();
        const vertMergedTds = tableInfo.rowspans[trElement.rowIndex] || [];
        const tdElements = trElement.children;

        let vertColspan = undefined;
        let count = vertMergedTds.length + tdElements.length;
        let idx = -1; // td index (vertMergedTds)
        let tdIdx = 0; //TD Element index 
        let spanedTdElement;

        while (count > 0) {
            count--;
            idx++;

            const vertMergedTdInfo = this.__getVertMergedTdInfo__(trElement.rowIndex, idx);
            if (!!vertMergedTdInfo) {
                // rowspan  td 
                spanedTdElement = this.__createEmptyTdElement__(vertMergedTdInfo.colspan, null);
                tableCells.push(this.convertToTableCell(spanedTdElement, !!vertColspan ? --vertColspan > 0 : false, true));

                if (!!vertMergedTdInfo.colspan) {
                    vertColspan = vertMergedTdInfo.colspan;
                }
                continue;
            }

            const tdElement = tdElements[tdIdx++];
            tableCells.push(this.convertToTableCell(tdElement));

            const colspan = !!tdElement.getAttribute('colspan') ? parseInt(tdElement.getAttribute('colspan')) : null;
            const rowspan = !!tdElement.getAttribute('rowspan') ? parseInt(tdElement.getAttribute('rowspan')) : null;

            // colspan  td 
            if (!!colspan) {
                const length = colspan - 1;
                for (let i = 0; i < length; i++) {
                    spanedTdElement = this.__createEmptyTdElement__(null, rowspan);
                    tableCells.push(this.convertToTableCell(spanedTdElement, true, false));
                }
                idx += length;
            }

            // rowspan  td   td    
            if (!!rowspan) {
                const cellIdx = tableCells.length - 1 - (!!colspan ? colspan - 1 : 0);
                let rowIdx = trElement.rowIndex + 1;

                for (let i = 0; i < rowspan - 1; i++) {
                    if (!tableInfo.rowspans[rowIdx]) {
                        tableInfo.rowspans[rowIdx] = [];
                    }
                    tableInfo.rowspans[rowIdx].push({
                        idx: cellIdx,
                        colspan: colspan
                    });

                    if (!!colspan) {
                        for (let j = 1; j < colspan; j++) {
                            tableInfo.rowspans[rowIdx].push({idx: cellIdx + j});
                        }
                    }
                    rowIdx++;
                }
            }
        }

        return tableCells;
    }

    /**
     * rowspan  td  .<br/>
     *    td  ,  td  colspan  .
     * @private
     * @param {Number} rowIdx  
     * @param {Number} colIdx  
     * @returns {Object|undefined} {idx: td, colspan: Number|undefined}  merge 
     */
    __getVertMergedTdInfo__(rowIdx, colIdx) {
        const vertMergedTds = this.__getTableInfo__().rowspans[rowIdx];
        if (!vertMergedTds) {
            return;
        }

        for (let i = 0; i < vertMergedTds.length; i++) {
            if (vertMergedTds[i].idx === colIdx) {
                return vertMergedTds[i];
            }
        }
    }

    /**
     * border  .     undefined .
     * @private
     * @param {Object} borderProp  
     * @returns {Object|undefined} border 
     */
    __getTableBorderObject__(borderProp) {
        if (!borderProp) {
            return;
        }

        return (borderProp.width || 0) !== 0 ? {
            style: borderProp.style,
            fill: 'color' in borderProp ? {
                type: 'SOLID_FILL',
                solid_fill: {
                    color: this.__getColorObject__(borderProp.color)
                }
            } : undefined,
            size_in_sunit: 'width' in borderProp ? borderProp.width : 500,
            space_in_sunit: 0
        } : { style: '_nill' };
    }

    /**
     * color  
     * @private
     * @param {Number} colorDecimal color decimal 
     * @returns {Object|undefined} colorDecimal   color  
     */
    __getColorObject__(colorDecimal) {
        if (colorDecimal === undefined) {
            return;
        }

        return {
            type: 'SRGB',
            srgb_color: {
                value_in_hex: colorDecimal
            }
        };
    }

    /**
     *   Paragraph  .<br/>
     *  element     Prop  Paragraph .
     * @private
     * @param {HTMLElement} [element] HTML Element
     * @returns {Object}  Paragraph 
     */
    __getDefaultParagraphObject__(element) {
        const defaultPara = {
            type: 'PARAGRAPH',
            paragraph: {
                prop: {},
                children: [],
                element_id: undefined
            }
        };

        if (!!element) {
            const paraProp = defaultPara.paragraph.prop;

            switch (this.tagList[element.nodeName]) {
                case 'TABLE':
                    util.set(paraProp, 'indent.first_in_sunit', 0);
                    util.set(paraProp, 'indent.first_type', 'none');
                    util.set(paraProp, 'indent.left_in_sunit', 0);
                    break;
                case 'HR':
                    util.set(paraProp, 'font_line_height', 1);
                    break;
            }
        }

        return defaultPara;
    }

    /**
     * layout prop  
     * @private
     * @param {HTMLElement} element 
     * @returns {Object} layout prop 
     */
    __getLayoutPropObject__(element) {
        const isPicture = this.tagList[element.nodeName] === 'PICTURE';
        const layoutProp = {
            type: 'Word',
            word_layout: {
                type: 'INLINE',
                inline: {
                    non_visual_drawing_prop: {
                        id: '1'
                    },
                    effect_extent: isPicture ? {
                        left_in_emu: 0,
                        top_in_emu: 0,
                        right_in_emu: 0,
                        bottom_in_emu: 9525
                    } : undefined,
                    non_visual_graphic_frame_prop: isPicture ? {
                        graphic_frame_locks: {
                            no_change_aspect: 1
                        }
                    } : undefined
                }
            }
        };

        return layoutProp;
    }

    /**
     * Relationship   .
     * @private
     * @param {HTMLElement} element
     * @returns {Object} relationship 
     * @example
     * // relationship  
     * {
     *      id: 'rId1',
     *      target: 'media/image1.png',
     *      targetMode: 'Internal',
     *      type: 'IMAGE'
     * }
     */
    __createRelationInfo__(element) {
        let target = '';
        let type = '';
        let method;

        // target && method
        if (!!element.getAttribute('src')) {
            target = element.getAttribute('src');

            if (!!target.match(/^(https?:\/\/)/)) {
                method = 'url';
            }
        }

        // type
        switch (element.nodeName) {
            case 'IMG':
                type = 'IMAGE';
                break;
        }

        const relationInfo = {
            id: `rId${++this.rIdSeq}`,
            target: target,
            targetMode: 'Internal',
            type: type
        };

        if (!!method) {
            relationInfo.method = method;
        }

        return relationInfo;
    }

    /**
     *  TD Element 
     * @private
     * @param {Number} [colspan]
     * @param {Number} [rowspan]
     * @returns {HTMLElement} TD Element
     */
    __createEmptyTdElement__(colspan, rowspan) {
        const emptyPara = this.__createEmptyParaElement__();
        const td = document.createElement('td');
        td.appendChild(emptyPara);

        if (!!colspan) {
            td.setAttribute('colspan', colspan);
        }
        if (!!rowspan) {
            td.setAttribute('rowspan', rowspan);
        }

        return td;
    }

    /**
     *  Paragraph Element 
     * @private
     * @returns {HTMLElement} P Element
     */
    __createEmptyParaElement__() {
        const emptyPara = document.createElement('p');
        return emptyPara;
    }

    /**
     *    .
     * @private
     * @param {HTMLElement} Element
     * @returns {Boolean} true||false
     */
    __checkEmptyChildren__(element) {
        return element.childElementCount === 0;
    }

    /**
     * html   Model  .
     *     html  .
     * @param {String} html
     * @returns {Array} modelArray
     */
    convert(html) {
        let elTmp = document.createElement('div');
        elTmp.innerHTML = html;
        let result = [];
        Array.from(elTmp.childNodes).forEach(child => {
            switch (this.tagList[child.nodeName]) {
                case 'PARAGRAPH':
                case 'HEADING':
                    result.push(this.convertToParagraph(child));
                    break;
                case 'LIST':
                    result.push(...this.convertToList(child));
                    break;
                case 'TABLE':
                    result.push(this.convertToDrawingObj(child));
                    break;
                case 'TEXTRUN':
                    result.push(this.convertToTextRun(child));
                    break;
            }
        });
        return result;
    }

    /**
     * SynapEditor publishing html Model  .
     * html     Model .
     * @param {HTMLElement} html
     * @param {CSSRuleList} cssRules {@link https://developer.mozilla.org/en-US/docs/Web/API/CSSRuleList|CSSRuleList}
     * @returns {JSONObject} body
     */
    convertToDocument(html, cssRules) {
        this.__init__();
        this.cssRules = cssRules; // css rule setting

        let docModel = {
            body: {
                children: []
            },
            numbering_list: {
                numbering: []
            },
            relationship_list: {}
        };
        let elTmp = document.createElement('div');
        elTmp.innerHTML = html;
        Array.from(elTmp.childNodes).forEach(child => {
            switch (this.tagList[child.nodeName]) {
                case 'PARAGRAPH':
                case 'HEADING':
                    docModel.body.children.push(this.convertToParagraph(child));
                    break;
                case 'LIST':
                    if(!this.__checkEmptyChildren__(child)) {
                        docModel.body.children.push(...this.convertToList(child));
                    }
                    break;
                case 'HR':
                case 'TABLE':
                    docModel.body.children.push(this.convertToDrawingObj(child));
                    break;
            }
        });
        docModel.numbering_list.numbering = this.numbering;
        docModel.relationship_list.relationship = this.relationship;

        return util.clearProp(docModel);
    }

    /**
     * UL Element   numberingId  (PARAGRAPH)  Model    .
     * @param {HTMLElement} listElement
     * @returns {Array} paragraphs
     */
    convertToList(listElement) {
        this.numberingLevel = [];
        const prop = this.__getListProp__(listElement);

        const length = this.numbering.push({
            numbering_id: prop.numberingId,
            multi_level_type: prop.multiLevelType,
            numbering_level: []
        });

        this.isLists = true;
        const paragraphs = this.__getListParagraphs__(listElement, prop.numberingId);

        // numberingLevel 9  addNumberingLevel .
        if (this.numberingLevel.length < 9) {
            this.__addNumberingLevel__();
        }

        this.numbering[length - 1].numbering_level = this.numberingLevel;
        this.isLists = false;

        return paragraphs;
    }

    /**
     * P,LI,OL Element   cssStyle  Model Prop , <br>
     * getParagraphChildren    Model    .
     * @param {any} paragraphElement
     * @returns {JSONObject} Paragraph
     */
    convertToParagraph(paragraphElement, numberingId) {
        const prop = this.__getParagraphProp__(paragraphElement);
        const childrenElement = this.__getParagraphChildren__(paragraphElement);
        const paragraph = {
            type: 'PARAGRAPH',
            paragraph: {
                prop: {
                    align: prop.textAlign,
                    spacing: {
                        line_in_percent: prop.lineHeight,
                        line_rule: !!prop.lineHeight ? 'multiple' : undefined
                    },
                    indent: {
                        left_in_sunit: prop.paddingLeft
                    },
                    font_line_height: 1,
                    para_style: prop.paraStyle
                },
                children: childrenElement
            }
        };

        const paragraphProp = paragraph.paragraph.prop;

        if (this.isLists) { //  .
            const numberingLevel = this.__getNumberingLevel__(paragraphElement);
            paragraphProp.numbering_ref_pr = {
                level: numberingLevel.level,
                numbering_id: numberingId
            };
        }
        paragraphProp.end_run_prop = this.__getEndrunProp__(paragraph.paragraph.children);

        return paragraph;
    }

    /**
     * HTML Element   cssStyle  Model Prop  TextRun .
     * @param {HTMLElement} element
     * @param {HTMLElement} [parentElement]    Element
     * @returns {JSONObject} TextRun
     */
    convertToTextRun(element, parentElement) {
        const prop = this.__getTextRunProp__(element, parentElement);

        if (!!prop.tab) { // tab 
            return this.__getTabTextRun__();
        }

        const textRun = {
            type: 'TEXTRUN',
            textrun: {
                prop: {
                    font_size_in_sunit: prop.fontSize,
                    fonts: prop.fontFamily ? {
                        ascii: prop.fontFamily
                    } : undefined,
                    italic: prop.italic ? 1 : undefined,
                    bold: prop.bold ? 1 : undefined,
                    underline: prop.underline ? {
                        style: 'single'
                    } : undefined,
                    strike: prop.strike ? {
                        style: 'single'
                    } : undefined,
                    text_fill: 'color' in prop ? {
                        type: 'SOLID_FILL',
                        solid_fill: {
                            color: this.__getColorObject__(prop.color)
                        }
                    } : undefined,
                    fill: 'backgroundColor' in prop ? {
                        type: 'SOLID_FILL',
                        solid_fill: {
                            color: this.__getColorObject__(prop.backgroundColor)
                        }
                    } : undefined
                },
                text: this.__getTextContent__(element)
            }
        };

        return textRun;
    }

    /**
     * IMG Element   picture Model .
     * @param {HTMLElement} imgElement
     * @returns {JSONObject} picture
     */
    convertToPicture(imgElement) {
        const prop = this.__getPictureProp__(imgElement);
        const layoutProp = this.__getLayoutPropObject__(imgElement);
        const relationship = this.__createRelationInfo__(imgElement);

        const picture = {
            type: 'PICTURE',
            picture: {
                prop: {
                    non_visual_prop: {
                        drawing_prop: {
                            id: '1'
                        },
                        picture_prop: undefined
                    },
                    shape_prop: {
                        geometry: {
                            type: 'prstGeom',
                            preset_geom: {
                                adjust_list: undefined,
                                shape_type: 'rect'
                            }
                        },
                        transform: {
                            offset: {
                                x_in_emu: 0,
                                y_in_emu: 0
                            },
                            size: {
                                width_in_emu: prop.width,
                                height_in_emu: prop.height
                            }
                        },
                        non_visual_prop: {
                            drawing_prop: {
                                id: '1'
                            }
                        }
                    },
                    blip_fill: {
                        blip: {
                            embed: relationship.id,
                            blip_effect: undefined
                        },
                        fill_mode: {
                            type: 'STRETCH',
                            stretch: {
                                source_rect: undefined
                            }
                        }
                    },
                },
                element_id: undefined,
                layout_prop: layoutProp
            }
        };

        this.relationship.push(relationship);

        return picture;
    }

    /**
     * Picture Model  
     * @param {HTMLElement} element Picture HTML Element (TABLE, ETC...)
     * @returns {JSONObject}
     */
    convertToDrawingObj(element) {
        let graphicObj, paragraph;

        switch (this.tagList[element.nodeName]) {
            case 'TABLE':
                paragraph = this.__getDefaultParagraphObject__(element);
                graphicObj = this.convertToTable(element);
                break;
            case 'PICTURE':
                graphicObj = this.convertToPicture(element);
                break;
            case 'HR':
                paragraph = this.__getDefaultParagraphObject__(element);
                graphicObj = this.convertToHorizontalLine(element);
                break;
        }

        const textrun = {
            type: 'TEXTRUN',
            textrun: {
                prop: undefined,
                drawing_obj: {
                    element_id: undefined,
                    layout_prop: this.__getLayoutPropObject__(element),
                    graphic_obj: !!graphicObj ? graphicObj : undefined
                }
            }
        };

        if (!!paragraph) {
            paragraph.paragraph.children.push(textrun);
        }

        return !!paragraph ? paragraph : textrun;
    }

    /**
     * HR Element   Model  .
     * @param {HTMLElement} hrElement HR Element
     * @returns {JSONObject} Horizontal line
     */
    convertToHorizontalLine(hrElement) {
        const horizontalLine = {
            type: 'HORZONTAL_LINE',
            horzontal_line: {
                prop: {
                    width: {
                        type: 'percent',
                        value_in_percent: 100000
                    },
                    height_in_sunit: 1500,
                    color: this.__getColorObject__(0) // 
                }
            }
        };

        return horizontalLine;
    }

    /**
     * TABLE Element   Model  .
     * @param {HTMLElement} tableElement TABLE HTML Element
     * @returns {JSONObject} Table
     */
    convertToTable(tableElement) {
        this.__setTableInfo__(tableElement);

        const tableRows = this.__getTableRows__(tableElement);
        const prop = this.__getTableProp__(tableElement);
        const table = {
            type: 'TABLE',
            table: {
                prop: {
                    width: {
                        type: prop.isAutoWidth ? '_auto' : 'sunit',
                        value: 0 // _auto  table width  ..
                    },
                    table_look: {
                        band_col: 0, //  
                        band_row: 1, //  
                        first_col: 1, //  
                        first_row: 1, //  
                        last_col: 0, //  
                        last_row: 0 //  
                    },
                    non_visual_prop: {
                        drawing_prop: {
                            id: "1"
                        }
                    }
                },
                table_row: tableRows
            }
        };

        this.__removeTableInfo__();

        return table;
    }

    /**
     * TR Element   Model  .
     * @param {HTMLElement} trElement TR HTML Element
     * @returns {JSONObject} TableRow
     */
    convertToTableRow(trElement) {
        const tableCells = this.__getTableCells__(trElement);
        const prop = this.__getTableRowProp__(trElement);

        const tableRow = {
            prop: {
                height: 'height' in prop ? {
                    value_in_sunit: prop.height
                } : undefined
            },
            table_cell: tableCells
        };

        return tableRow;
    }

    /**
     * TD Element   Model  .
     * @param {HTMLElement} tdElement
     * @param {Number} [isHorzMerged=false]   
     * @param {Number} [isVertMerged=false]   
     * @return {JSONObject} TableCell
     */
    convertToTableCell(tdElement, isHorzMerged = false, isVertMerged = false) {
        const paragraphs = this.__getTableCellParagraphs__(tdElement);
        const prop = this.__getTableCellProp__(tdElement);

        const tableCell = {
            prop: {
                col_span: prop.colspan,
                horz_merged: isHorzMerged ? 1 : undefined,
                row_span: prop.rowspan,
                vert_merged: isVertMerged ? 1 : undefined,
                borders: {
                    top: this.__getTableBorderObject__(prop.borderTop),
                    left: this.__getTableBorderObject__(prop.borderLeft),
                    bottom: this.__getTableBorderObject__(prop.borderBottom),
                    right: this.__getTableBorderObject__(prop.borderRight)
                },
                fill: 'backgroundColor' in prop ? {
                    type: 'SOLID_FILL',
                    solid_fill: {
                        color: this.__getColorObject__(prop.backgroundColor)
                    }
                } : undefined,
                width: !!prop.width ? {
                    type: prop.widthType,
                    value: prop.width
                } : { type: '_auto', value: 11100 } //_auto     
            },
            paragraph: paragraphs
        };

        return tableCell;
    }
}

export default HTMLToModel;


