import TextRun from './TextRun';

/**
 * BookmarkEnd
 * @extends TextRun
 */
class BookmarkEnd extends TextRun {
    /**
     * Element Type .
     * @returns {String} BookmarkEnd
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'BookmarkEnd';
    }

    /**
     * BookmarkEnd length .
     * @returns {Number} length
     */
    length() {
        return 0;
    }

    /**
     * BookmarkStart split    .
     */
    split() {
        const run = TextRun.createTextRun();
        this.after(run);
        return run;
    }
}

export default BookmarkEnd;


