import $ from 'jquery';

/**
 *  Popup   .
 */
class Popup {
    /**
     * Creates an instance of Popup.
     * @param {any} editorId -      
     * @param {any} name - Popup 
     * @param {any} width - Popup 
     * @param {any} height - Popup 
     */
    constructor(editorId, name, width, height) {
        this.editorId = editorId;
        this.width = width;
        this.height = height;
        this.name = name;

        this.target = undefined; //       . (         )
        this.$element = undefined; // popup jQuey Element.
        this.fnHideCallback = undefined; // hide()     . (1)

        this.bUseConfirmBtn = true; //    flag, Popup        .
        this.bUseCloseBtn = true; //    flag, Dialog        .
        this.bUseHeader = true; //Header   flag, Dialog        .
        this.bUseFooter = true; //Footer   flag, Dialog        .
    }

    /**
     *   .
     * @returns {Number} - 
     */
    getWidth() {
        return this.width;
    }

    /**
     *   .
     * @returns {Number} - 
     */
    getHeight() {
        return this.height;
    }

    /**
     *   .
     * @returns {String} - 
     */
    getName() {
        return this.name;
    }

    /**
     *  position .
     * @returns {Object} position
     * @example 
     * // position 
     * {
     *      top: Number,
     *      left: Number
     * };
     */
    getPosition() {
        const element = this.getElement().get(0);

        return {
            top: element.offsetTop,
            left: element.offsetLeft
        };
    }

    /**
     *  HTML Element width, Height .
     * @returns {Object} Size
     * @example
     * // size 
     * {
     *      width: Number,
     *      height: Number
     * }
     */
    getSize() {
        const element = this.getElement().get(0);

        return {
            width: element.offsetWidth,
            height: element.offsetHeight
        };
    }

    /**
     *  jQuery Element .
     * @returns {jQueryElement}
     */
    getElement() {
        return this.$element;
    }

    /**
     * Popup Dom  Popup Container .
     */
    createDom() {
        const popupHTML = `
            <div class='se-popup' style='min-width: ${this.getWidth()}px; min-height: ${this.getHeight()}px;'>
                <div class='se-popup-arrow'></div>
            </div>
        `;
        const $popup = $(popupHTML);

        //Header 
        const headerElement = this.getHeaderElement();
        if (this.bUseHeader) {
            headerElement.addClass('show');
        }
        $popup.append(headerElement);

        //Body 
        $popup.append(this.getBodyElement());

        //Footer 
        if (this.bUseFooter) {
            $popup.append(this.getFooterElement());
        }

        //Container Popup 
        let $popupContainer = $('#' + this.editorId).find('.se-popup-container');
        $popupContainer.append($popup);

        this.$element = $popup;
    }

    /**
     *  Header   jQueryElement .
     * @returns {jQueryElement}
     */
    getHeaderElement() {
        const closeHTML = this.bUseCloseBtn ? `<span class='se-popup-close'>X</span>` : '';
        const backHTML = `<span class='se-popup-back'></span>`;

        return $(`<div class='se-popup-header'>${backHTML}${closeHTML}</div>`);
    }

    /**
     *  body   jQueryElement .
     * Popup   getBodyElement()  .
     * @returns {jQueryElement}
     */
    getBodyElement() {
        // do override this
        return $(`<div class='se-popup-body'>`);
    }

    /**
     *  Footer   jQueryElement .
     * @returns {jQueryElement}
     */
    getFooterElement() {
        const confirmHTML = this.bUseConfirmBtn ? `<span class='se-popup-confirm se-popup-btn'></span>` : '';
        return $(`<div class='se-popup-footer'>${confirmHTML}</div>`);
    }

    /**
     *   . 
     * @param {Object} [position]    
     * @example 
     * // position 
     * {
     *      top: Number,
     *      left: Number
     * };
     */
    setPosition(position) {
        this.getElement().css(position);
    }

    /**
     *  .
     * @param {HTMLElement} [position]   
     * @param {Boolean} [isBalloonAction=false]     
     */
    toggle(position, isBalloonAction = false) {
        const $popup = this.getElement();

        if ($popup.hasClass('show')) {
            this.hide();
        } else {
            this.show(position, isBalloonAction);
        }
    }

    /**
     *  .
     * @param {HTMLElement} target    
     * @param {HTMLElement} [position]   
     * @param {Function} [fnHideCallback]    
     */
    toggleWithTarget(target, position, fnHideCallback) {
        const isBalloonAction = !!$(target).parents('.se-popup').get(0);

        this.setHideCallback(fnHideCallback);

        if (this.target !== target) {
            this.target = target;
            this.show(position, isBalloonAction);
        } else {
            this.toggle(position, isBalloonAction);
        }
    }

    /**
     *  .
     * @param {Object} [position]   
     * @param {Boolean} [isBalloonAction=false]     
     * @example
     * // position
     * var position = {
     *      top: 10,
     *      left: 10
     * };
     * 
     * popup.show(position);
     */
    show(position, isBalloonAction = false) {
        if (!!position) {
            this.setPosition(position);
        }
        this.reconfigureHeader(isBalloonAction);

        this.getElement().addClass('show');
    }

    /**
     *  .
     */
    hide() {
        this.getElement().removeClass('show');

        if (!!this.fnHideCallback) {
            this.fnHideCallback.call();
            this.fnHideCallback = undefined;
        }
    }

    /**
     *      .
     */
    confirm() {
        this.hide();
    }

    /**
     *      .
     */
    cancel() {
        this.hide();
    }

    /**
     *      .
     */
    back() {
        this.hide();
    }

    /**
     * hide()      .
     * @param {Function} fn  
     */
    setHideCallback(fn) {
        this.fnHideCallback = fn;
    }

    /**
     *    . <br/>
     * (        )
     * @param {Boolean} isBalloonAction     
     */
    reconfigureHeader(isBalloonAction) {
        const $element = this.getElement();

        if (isBalloonAction) {
            //    
            $element.find('.se-popup-header, .se-popup-back').addClass('show');
        } else {
            //  / &&   
            if (!this.bUseHeader) {
                $element.find('.se-popup-header').removeClass('show');
            }
            $element.find('.se-popup-back').removeClass('show');
        }
    }

    /**
     *  .
     */
    attachEvent() {
        const $popup = this.getElement();

        $popup.on('click', '.se-popup-confirm', this.confirm.bind(this));
        $popup.on('click', '.se-popup-close', this.cancel.bind(this));
        $popup.on('click', '.se-popup-back', this.back.bind(this));
    }

    /**
     *  .
     */
    detachEvent() {
        const $popup = this.getElement();

        $popup.off('click', '.se-popup-confirm');
        $popup.off('click', '.se-popup-close');
        $popup.off('click', '.se-popup-back');
    }
}

export default Popup;


