/**
 *      .
 * @private
 * @returns {Array}
 */
function getFontSizeItems() {
    const fontSizes = [8, 9, 10, 11, 12, 14, 16, 18, 20, 24, 28, 32, 36, 40, 44, 48, 54, 60, 72, 80, 88, 96];

    return fontSizes.map(fontSize => {
        return {
            label: fontSize,
            cssText: 'font-size: ' + fontSize + 'pt;',
            actionValue: fontSize * 1000,
            actionName: actionMap.FONTSIZE
        };
    });
}

/**
 *      .
 * @private
 * @returns {Array}
 */
function getLineHeightItems() {
    const lineHeights = [0, 100, 120, 140, 160, 180, 200, 250, 300];

    return lineHeights.map(lineHeight => {
        return {
            label: lineHeight === 0 ? '' : lineHeight + '%',
            actionValue: lineHeight || 120,
            actionName: actionMap.SET_LINE_HEIGHT
        };
    });
}

