import TextRun from './TextRun';

class Break extends TextRun {
    /**
     * Break Type  TextRun Break  .
     * @param {String} type 
     * @returns {TextRun} textRun Break
     */
    static createBreak(type) {
        return new Break({break: {type: type, clear:'none'}});
    }

    /**
     * Break length .
     * @returns {Number}
     */
    length() {
        return 1;
    }

    /**
     *  TextRun  index  Break    TextRun .
     * @param {Number} index 
     * @return {TextRun} run
     */
    split(index) {
        let run = TextRun.createTextRun();
        if (index === 0) {
            run.insertBefore(this);
        } else {
            run.insertAfter(this);
        }
        return run;
    }

}

export default Break;


