import util from '../util/util';
import consts from '../consts/consts';

const LISTS_FORMAT = consts.LISTS_FORMAT;
const LISTS_TYPE = consts.LISTS_TYPE;
const LISTS_MULTI_LEVEL_TYPE = consts.LISTS_MULTI_LEVEL_TYPE;
const LISTS_LEVEL_TEXT = consts.LISTS_LEVEL_TEXT;
const LISTS_LEVEL = consts.LISTS_LEVEL;
const MULTI_LISTS_STYLE = consts.MULTI_LISTS_STYLE;
const HORIZONTAL_ALIGNMENT = consts.HORIZONTAL_ALIGNMENT;
const LISTS_INDENT_STEP = consts.LISTS_INDENT_STEP;

/**
 *  (numbering_list)   Class.
 */
class ListsManager {
    constructor() {
        this.lists = null;
        this.maxId = 0;
    }

    /**
     * docModel     .
     * @param {Array} lists -    
     */
    setLists(lists) {
        this.lists = util.cloneDeep(lists) || [];
        this.__init__();
    }

    /**
     *   .
     * @returns {Array} -    
     */
    getLists() {
        return this.lists;
    }

    /**
     *     .
     * @param {Object} item
     */
    appendItem(item) {
        this.lists.push(item);
    }

    /**
     *       .
     * @private
     */
    __init__() {
        this.lists.forEach(item => {
            const id = util.get(item, ['numbering_id'], 0);
            this.maxId = Math.max(this.maxId, id);

            util.get(item, ['numbering_level']).forEach((levelItem, level) => {
                let indentStep = util.get(levelItem, ['para_prop', 'indent', 'left_in_sunit']);

                if (indentStep > 0) {
                    indentStep = LISTS_INDENT_STEP * (level + 1);
                }

                util.set(levelItem, ['para_prop', 'indent', 'left_in_sunit'], indentStep);
                util.set(levelItem, ['para_prop', 'indent', 'first_type'], 'none');
                util.unset(levelItem, ['para_prop', 'indent', 'first_in_sunit']);
            });
        });
    }

    /**
     *     .
     * @returns {Number}
     */
    makeNewId() {
        return ++this.maxId;
    }

    /**
     * docModel      .
     * @param {Array} -      
     */
    setPictureLists(pictureLists) {
        this.pictureLists = pictureLists;
    }

    /**
     *     .
     * @returns {Array} -      
     */
    getPictureLists() {
        return this.pictureLists;
    }

    /**
     *     .
     * @param {Number} id -   id
     * @param {Number} level -   level
     * @param {Number} value -  
     */
    setStartValue(id, level, value) {
        const levelItem = this.getLevelItem(id, level);
        if (levelItem) {
            util.set(levelItem, ['start_value'], value);
        }
    }

    /**
     *     .
     * @param {Number} id   id
     * @param {Number} level   level
     * @returns {Number}
     */
    getStartValue(id, level) {
        let levelItem = this.getLevelItem(id, level);
        return util.get(levelItem, ['start_value'], 1);
    }

    /**
     *    .
     * @param {Number} id
     * @param {Number} level
     * @param {String} format
     * @param {String} levelText
     */
    setListsItem(id, level, format, levelText) {
        let item = this.getListsItem(id);

        if (item) {
            if (format === LISTS_FORMAT.BULLET) {
                util.set(item, ['numbering_level', level, 'textrun_prop', 'fonts'], makeBulletFonts());
            } else {
                levelText = levelText || convertLevelText(format, level);
                util.unset(item, ['numbering_level', level, 'textrun_prop', 'fonts']);
            }

            util.set(item, ['numbering_level', level, 'level_text'], levelText);
            util.set(item, ['numbering_level', level, 'format'], format);
        } else {
            item = makeListsItem(id, level, format, levelText);
            this.appendItem(item);
        }
    }

    /**
     *    .
     * @param {Number} id
     * @param {String} multiListsStyle
     */
    setMultiListsItem(id, multiListsStyle) {
        let item = this.getListsItem(id);

        if (item) {
            util.set(item, ['multi_level_type'], LISTS_MULTI_LEVEL_TYPE.MULTI_LEVEL);
            util.set(item, ['numbering_level'], makeMultiListsLevelItems(multiListsStyle));
        } else {
            item = makeMultiListsItem(id, multiListsStyle);
            this.appendItem(item);
        }
    }

    /**
     * id     .
     * @private
     * @param {Number} id   id
     * @returns {Object}
     */
    getListsItem(id) {
        return util.find(this.lists, util.matchesProperty('numbering_id', id));
    }

    /**
     *     .
     *    ,      .
     * @param {Number} id   id
     * @returns {String}
     */
    getListsAlign(id) {
        let multiLevelType = this.getMultiLevelType(id);
        return multiLevelType === LISTS_MULTI_LEVEL_TYPE.HYBRID_MULTI_LEVEL ? HORIZONTAL_ALIGNMENT.RIGHT : HORIZONTAL_ALIGNMENT.LEFT;
    }

    /**
     *     .
     * multiLevel =  
     * hybridMultilevel =  ,  
     * @param {Number} id   id
     * @returns {String}
     */
    getMultiLevelType(id) {
        return util.get(this.getListsItem(id), ['multi_level_type'], LISTS_MULTI_LEVEL_TYPE.HYBRID_MULTI_LEVEL);
    }

    /**
     *   levelItem .
     * @param {Number} id   id
     * @param {Number} level   level
     * @returns {Object}
     */
    getLevelItem(id, level) {
        let listsItem = this.getListsItem(id);
        return util.get(listsItem, ['numbering_level', level]);
    }

    /**
     *   level text .
     * @param {Number} id   id
     * @param {Number} level   level
     * @returns {String}
     */
    getLevelText(id, level) {
        let levelItem = this.getLevelItem(id, level);
        return util.get(levelItem, ['level_text']);
    }

    /**
     *      .
     * bullet / chosung / decimal / decimalEnclosedCircle
     * decimalFullWidth / ganada / none / upperLetter
     * lowerLetter / upperRoman / lowerRoman
     * @param {Number} id   id
     * @param {Number} level   level
     * @returns {String}
     */
    getListsFormat(id, level) {
        let levelItem = this.getLevelItem(id, level);
        return util.get(levelItem, ['format']);
    }

    /**
     *    .
     * @param {Number} id   id
     * @param {Number} level   level
     * @returns {String} bullet or number
     */
    getListsType(id, level) {
        let format = this.getListsFormat(id, level);
        let type = format ? (format === LISTS_FORMAT.BULLET ? LISTS_TYPE.BULLET : LISTS_TYPE.NUMBER) : undefined;
        return type;
    }
}

/**
 *         levelText   .
 * @private
 * @param {String} format
 * @param {Number} level
 * @returns {String}
 */
function convertLevelText(format, level) {
    let levelText;

    switch (format) {
        case LISTS_FORMAT.DECIMAL_ENCLOSED_CIRCLE:
            levelText = `%${level + 1}`;
            break;
        case LISTS_FORMAT.DECIMAL:
        case LISTS_FORMAT.UPPER_LETTER:
        case LISTS_FORMAT.LOWER_LETTER:
        case LISTS_FORMAT.UPPER_ROMAN:
        case LISTS_FORMAT.LOWER_ROMAN:
        case LISTS_FORMAT.GANADA:
        case LISTS_FORMAT.CHOSUNG:
            levelText = `%${level + 1}.`;
            break;
        case LISTS_FORMAT.BULLET:
            if (level % 3 === 0) {
                levelText = LISTS_LEVEL_TEXT.DISC;
            } else if (level % 3 === 1) {
                levelText = LISTS_LEVEL_TEXT.CIRCLE;
            } else {
                levelText = LISTS_LEVEL_TEXT.SQUARE;
            }
            break;
    }

    return levelText;
}

/**
 *    numbering_list   .
 * @private
 * @param {Number} id
 * @param {Number} level
 * @param {String} format
 * @param {String} paramLevelText
 * @returns {JSON}
 */
function makeListsItem(id, level, format, paramLevelText) {
    const numberingLevels = [];
    let levelText;

    for (let i = LISTS_LEVEL.MIN; i <= LISTS_LEVEL.MAX; i++) {
        if (level === i) {
            levelText = paramLevelText || convertLevelText(format, i);
        } else {
            levelText = convertLevelText(format, i);
        }

        numberingLevels.push(makeLevelItem(i, format, levelText));
    }

    return {
        numbering_id: id,
        multi_level_type: LISTS_MULTI_LEVEL_TYPE.HYBRID_MULTI_LEVEL,
        numbering_level: numberingLevels
    };
}

/**
 *    numbering_list         .
 * @private
 * @param {Number} level
 * @param {String} format
 * @param {String} levelText
 * @returns {Object}
 */
function makeLevelItem(level, format, levelText) {
    let levelItem = {
        level: level,
        start_value: 1,
        format: format,
        level_text: levelText,
        align: 'LEFT',
        para_prop: {
            indent: {
                left_in_sunit: LISTS_INDENT_STEP * (level + 1),
                first_type: 'none'
            }
        }
    };

    if (format === LISTS_FORMAT.BULLET) {
        levelItem['textrun_prop'] = {
            fonts: makeBulletFonts()
        };
    }

    return levelItem;
}

/**
 *       .
 * @private
 * @returns {JSON}
 */
function makeBulletFonts() {
    return {
        ascii: 'Arial',
        high_ansi: 'Arial',
        hint: '_default'
    };
}

/****************************************************************************************
 ******************************      **********************************
 ****************************************************************************************/
/**
 *     .
 * @private
 * @param {Number} id
 * @param {String} style - multi_1, multi_2, multi_3
 * @return {Object}
 */
function makeMultiListsItem(id, style) {
    return {
        numbering_id: id,
        multi_level_type: LISTS_MULTI_LEVEL_TYPE.MULTI_LEVEL,
        numbering_level: makeMultiListsLevelItems(style)
    }
}

/**
 *      .
 * @private
 * @param {String} style - multi_1, multi_2, multi_3
 * @return {Array}
 */
function makeMultiListsLevelItems(style) {
    const levelItems = [];

    for (let level = LISTS_LEVEL.MIN; level <= LISTS_LEVEL.MAX; level++) {
        levelItems.push(makeMultiListsLevelItem(style, level));
    }

    return levelItems;
}

/**
 *        .
 * @private
 * @param {String} style - multi_1, multi_2, multi_3
 * @param {Number} level
 * @return {Object}
 */
function makeMultiListsLevelItem(style, level) {
    let levelText = '';
    let indent;

    if (style === MULTI_LISTS_STYLE.MULTI_1) {
        indent = {
            left_in_sunit: LISTS_INDENT_STEP * level,
            first_type: 'none'
        };

        for (let i = 0; i <= level; i++) {
            levelText += `%${i + 1}.`;
        }
    } else if (style === MULTI_LISTS_STYLE.MULTI_2) {
        indent = {
            left_in_sunit: 0,
            first_type: 'none'
        };

        for (let i = 0; i <= level; i++) {
            levelText += `%${i + 1}.`;
        }
    } else if (style === MULTI_LISTS_STYLE.MULTI_3) {
        indent = {
            left_in_sunit: 0,
            first_type: 'none'
        };

        for (let i = 0; i <= level; i++) {
            levelText += (i === level ? `%${i + 1}` : `%${i + 1}-`);
        }
    }

    return {
        level: level,
        start_value: 1,
        format: LISTS_FORMAT.DECIMAL,
        level_text: levelText,
        align: 'LEFT',
        para_prop: {indent}
    };
}

/****************************************************************************************
 ******************************      **********************************
 ****************************************************************************************/

export default ListsManager;


