import Builder from '../model/Builder';
import util from '../util/util';

/**
 *   Class.<br/>
 *     Builder .<br/>
 *  Document, Body, Paragraph, NumberingList        API .
 * @example
 * import SEModelManager from 'path';
 * var SEModelManager = SEModelManager.getInstnace();
 */
class SEModelManager {
    /**
     * SEModelManager.getInstnace()  . <br/>
     * new SEModelManager()    .
     */
    constructor() {
        this.SEModel = null;
        this.body = null;
        this.builder = new Builder();
    }

    /**
     *   (SEModel) .
     * @param {Object} docModel  ( )
     */
    setSEModel(docModel) {
        this.SEModel = this.builder.buildDocument(docModel);
        this.body = this.SEModel.body;
    }

    /**
     *  .
     * @returns {SEModel} 
     */
    getSEModel() {
        if (!this.SEModel) {
            throw new Error('Not have document');
        }
        return this.SEModel;
    }

    /**
     *  Body Element .
     * @returns {Body} Body Element
     */
    getBody() {
        if (!this.body) {
            throw new Error('Not have body');
        }
        return this.body;
    }

    /**
     * id Paragraph Element  .
     * @param {Number} id Paragraph ID
     * @returns {Paragraph} Paragraph Element
     */
    getParagraph(id) {
        const paragraph = this.getBody().findById(id);
        return util.isParagraph(paragraph) ? paragraph : null;
    }

    /**
     * id Picture Element  .
     * @param {Number} id Picture ID
     * @returns {Picture} Picture Element
     */
    getPicture(id) {
        const picture = this.getBody().findById(id);
        return util.isPicture(picture) ? picture : null;
    }

    /**
     * id Table Element  .
     * @param {Number} id Table ID
     * @returns {Table} Table Element
     */
    getTable(id) {
        const table = this.getBody().findById(id);
        return util.isTable(table) ? table : null;
    }

    /**
     *       paragraph . 
     * @param {Number} listsId -   
     * @returns {Array} 
     */
    getParagraphsByListsId(listsId) {
        return this.getAllParagraph(true).filter(paragraph => {
            return paragraph.getListsId() > 0 && paragraph.getListsId() === listsId;
        });
    }

    /**
     *  Paragraph  .
     * @returns {Array} [Paragraph]{@link Paragraph}
     */
    getAllParagraph(deep = false) {
        let startElement = this.getFirstParagraph();
        let endElement = this.getLastParagraph();
        let element = startElement;
        let paragraphs = [];
        
        while (element) {
            if (util.isParagraph(element)) {
                if (deep) {
                    element.getChildren().forEach(run => {
                        if (util.isTable(run)) {
                            paragraphs.push(...run.getParagraphs());
                        }
                    });
                }
                paragraphs.push(element);
            }
            if (element === endElement) {
                break;
            }
            element = element.nextSibling();
        }
        
        return paragraphs;
    }

    /**
     *   Paragraph .
     * @returns {Paragraph} Paragraph Element
     */
    getFirstParagraph() {
        return this.firstParagraphOf(this.getBody());
    }

    /**
     *   Paragraph .
     * @returns {Paragraph} Paragraph Element
     */
    getLastParagraph() {
        return this.lastParagraphOf(this.getBody());
    }

    /**
     *  element  paragraph element .
     * @param {Element} element
     * @returns {Paragraph}
     */
    previousParagraphOf(element) {
        let prev;
        let parent = element;
        if (util.isParagraph(element)) {
            prev = element.previousSibling();
        }
        while (!prev) {
            parent = parent.parent();
            if (util.isParagraph(parent)) {
                return parent;
            }
            if (util.isRun(parent)) {
                parent = parent.parent();
            }
            if (parent.isRoot()) {
                return null;
            }
            prev = parent.previousSibling();
        }
        return util.isParagraph(prev) ? prev : this.lastParagraphOf(prev);
    }
    
    /**
     *  element  paragraph element .
     * @param {Element} element
     * @returns {Paragraph}
     */
    nextParagraphOf(element) {
        let next;
        let parent = element;
        // TODO: paragraphs in element
        if (util.isParagraph(element)) {
            next = element.nextSibling();
        }
        while (!next) {
            parent = parent.parent();
            if (util.isRun(parent)) {
                parent = parent.parent();
            }
            if (parent.isRoot()) {
                return null;
            }
            next = parent.nextSibling();
        }
        return util.isParagraph(next) ? next : this.firstParagraphOf(next);
    }
    
    /**
     *  element    paragraph element .
     * @param {Element} element 
     * @returns {Paragraph}
     */
    firstParagraphOf(element) {
        return element.findFirst(util.isParagraph);
    }

    /**
     *  element    paragraph element .
     * @param {Element} element 
     * @returns {Paragraph}
     */
    lastParagraphOf(element) {
        return element.findLast(util.isParagraph);
    }

    /**
     *   Element  Id .
     * @returns {Number} New Element Id
     */
    getNewElementId() {
        return this.builder.getNewElementId();
    }

    /**
     * Hyperlink  .
     * @returns {Object} Hyperlink Object {id : hyperlink model}
     */
    getHyperlinkMap() {
        return this.builder.getHyperlinkMap();
    }

    /**
     * Id  Hyperlink .
     * @param {Number} id
     * @return {Object} hyperlink
     */
    getHyperlink(id) {
        return this.builder.getHyperlinkMap()[id];
    }

    /**
     * id   Hyperlink .
     * @param {Number} id 
     */
    deleteHyperlink(id) {
        this.builder.deleteHyperlink(id);
    }

    /**
     * Id  hyperlink .
     * @param {Number} id 
     * @param {HyperlinkStart} hyperlinkStart
     * @param {HyperlinkEnd} hyperlinkEnd
     */
    setHyperlink(id, hyperlinkStart, hyperlinkEnd) {
        this.builder.setHyperlink(id, hyperlinkStart, hyperlinkEnd);
    }

    /**
     *    . 
     * @param {Number} listsId 
     * @param {Paragraph} paragraph 
     * @returns {Number}
     */
    getIndexFromLists(listsId, paragraph) {
        const paragraphs = this.getParagraphsByListsId(listsId);
        return paragraphs.indexOf(paragraph);
    }
}

export default SEModelManager;


