import TextRun from './TextRun';

/**
 * BookmarkStart
 * @extends TextRun
 */
class BookmarkStart extends TextRun {
    /**
     * Element Type .
     * @returns {String} BookmarkStart
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'BookmarkStart';
    }
    
    /**
     * BookmarkStart length .
     * @returns {Number} length
     */
    length() {
        return 0;
    }

    /**
     * BookmarkStart split    .
     */
    split() {
        const run = TextRun.createTextRun();
        this.after(run);
        return run;
    }
}

export default BookmarkStart;


