/**
 *   class.
 */
const localeManager = {
    /**
     *  ,   ,    
     *      .
     * @param {String} defaultLanguage    
     * @param {String} userLanguage   
     * @returns {String}    (ko, en, ja ...)
     */
    getLanguage: function (defaultLanguage, userLanguage) {
        // userLanguage      .
        if (!!window.SE_LOCALE_MESSAGES[userLanguage]) {
            return userLanguage;
        }

        //       .
        const browserLanguage = this.getBrowserLanguage();
        if (!!window.SE_LOCALE_MESSAGES[browserLanguage]) {
            return browserLanguage;
        }

        //       ,      
        // defaultLanguage  (  ) .
        return !!window.SE_LOCALE_MESSAGES[defaultLanguage] ? defaultLanguage : 'en';
    },

    /**
     *     .
     * @returns {String}    (ko, en, ja, ...)
     */
    getBrowserLanguage: function () {
        const lang = navigator.language || navigator.userLanguage;

        return lang.substring(0, 2);
    },

    /**
     *     .
     * @param {String} editorId  ID
     * @returns {Object|undefined}  
     */
    getLocaleMessages: function (editorId) {
        const info = window.SE_INFO[editorId];

        if (!!info) {
            return window.SE_LOCALE_MESSAGES[info.language];
        }
    }
};

export default localeManager;


