import consts from '../consts/consts';

const UNDO_REDO_MAX_SIZE = consts.UNDO_REDO_MAX_SIZE;

/**
 * Undo/Redo Class .
 */
class UndoRedo {
    constructor() {
        this.undoStack = [];
        this.redoStack = [];
    }

    /**
     * undoStack .
     * @returns {Array} undoStack
     */
    getUndoStack() {
        return this.undoStack;
    }
    
    /**
     * redoStack .
     * @returns {Array} redoStack
     */
    getRedoStack() {
        return this.redoStack;
    }

    /**
     * Undo Stack   .
     * Undo Stack list     .
     */
    undo() {
        if (this.isEmptyUndo()) {
            return;
        }

        const item = this.undoStack.pop();
        this.__pushRedo__(item, true);

        return item;
    }

    /**
     * Redo Stack   .
     * Redo Stack list     .
     */
    redo() {
        if (this.isEmptyRedo()) {
            return;
        }

        const item = this.redoStack.pop();
        this.pushUndo(item);

        return item;
    }

    /**
     * Undo Stack  .
     * @returns {Boolean}
     */
    isEmptyUndo() {
        return this.undoStack.length === 0;
    }

    /**
     * Redo Stack  .
     * @returns {Boolean}
     */
    isEmptyRedo() {
        return this.redoStack.length === 0;
    }

    /**
     * Undo Stack   .
     * @returns {Boolean}
     */
    isFullUndo() {
        return this.undoStack.length === UNDO_REDO_MAX_SIZE;
    }

    /**
     * Undo Stack Item .
     * @param {Object} item 
     */
    pushUndo(item) {
        if (this.isFullUndo()) {
            this.undoStack.shift();
        }
        this.undoStack.push(item);
    }

    /**
     * Redo Stack Item .
     * @param {Object} item 
     */
    __pushRedo__(item) {
        this.redoStack.push(item);
    }

    /**
     * Redo Stack .
     */
    clearRedo() {
        this.redoStack.length = 0;
    }
}

export default UndoRedo;


