import util from '../util/util';
import DrawingObject from './DrawingObject';
/**
 * Picture
 * @extends DrawingObject
 */
class Picture extends DrawingObject {
    /**
     * Element Type .
     * @returns {String} Picture
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'Picture';
    }

    /**
     *  export , SEModel docModel    .<br/>
     * Picture Element  docModel drawingObject prop   ,<br/>
     * picture prop   Element DrawingObject toPbModel .<br/>
     *  docModel  picture  JSON Object .
     * @returns {Object} picture JSON Object
     */
    toPbModel() {
        let picture = {
            "picture": {
                "prop": this.prop
            },
            "type": "PICTURE"
        };
        return super.toPbModel(picture);
    }

    /**
     * Picture Element  .
     * @returns {Number}
     */
    getId () {
        return this.element_id;
    }

    /**
     * Picture SEModel width .
     * @param {Number} width 
     */
    setWidth(width) {
        util.set(this.prop, ['shape_prop', 'transform', 'size', 'width_in_emu'], util.px2emu(width));
    }

    /**
     * Picture SEModel height .
     * @param {Number} height 
     */
    setHeight(height) {
        util.set(this.prop, ['shape_prop', 'transform', 'size', 'height_in_emu'], util.px2emu(height));
    }

    /**
     * Picture SEModel rotation .
     * @param {Number} rotation 
     */
    setRotation(rotation) {
        util.set(this.prop, ['shape_prop', 'transform', 'rotation_in_angle'], rotation);
    }

    /**
     * Picture hyperlink prop target .
     * @param {String} url 
     */
    setHyperlinkTarget(url) {
        if (url) {
            const type = util.validateWebpageUrl(url) ? 'WEB' : util.validateEmailAdress(url) ? 'Email' : '';
            const target = (type === 'WEB' && url.indexOf('http') !== 0) ? 'http://' + url : url;
            
            util.set(this.layout_prop, ['word_layout', 'inline', 'non_visual_drawing_prop', 'hyperlink_on_click', 'target'], target);
            util.set(this.layout_prop, ['word_layout', 'inline', 'non_visual_drawing_prop', 'hyperlink_on_click', 'type'], type);
        } else {
            util.unset(this.layout_prop, ['word_layout', 'inline', 'non_visual_drawing_prop', 'hyperlink_on_click']);
        }
    }

    /**
     * Picture Element  . 
     * @returns {Object}
     */
    getProp() {
        return this.prop;
    }

    /**
     * Picture Element relationshipId .
     * @returns {String}
     */
    getRelationshipId() {
        return util.get(this.prop, ['blip_fill', 'blip', 'embed']);
    }

    /**
     * Picture Element Hyperlink Target .
     * @returns {String} hyperlink Target
     */
    getHyperlinkTarget() {
        return util.get(this, ['layout_prop', 'word_layout', 'inline', 'non_visual_drawing_prop', 'hyperlink_on_click', 'target']) || '';
    }

    /**
     * Picture   .
     * @static
     * @param {Number} elementId 
     * @param {Number} relationhipIndex 
     * @returns {Picture}
     */
    static createPicture(elementId, relationhipIndex) {
        const docModel = {
            "element_id": elementId,
            "layout_prop": {
                "type": "Word",
                "word_layout": {
                    "type": "INLINE",
                    "inline": {
                        "effect_extent": {
                            "left_in_emu": 0,
                            "top_in_emu": 0,
                            "right_in_emu": 0,
                            "bottom_in_emu": 0
                        },
                        "non_visual_drawing_prop": {
                            "id": relationhipIndex,
                            "name": " " + relationhipIndex
                        },
                        "non_visual_graphic_frame_prop": {
                            "graphic_frame_locks": {
                                "no_change_aspect": 1
                            }
                        }
                    }
                }
            },
            "prop": {
                "non_visual_prop": {
                    "drawing_prop": {
                        "id": relationhipIndex,
                        "name": null
                    }
                },
                "blip_fill": {
                    "blip": {
                        "embed": "rId" + relationhipIndex
                    },
                    "fill_mode": {
                        "type": "STRETCH",
                        "stretch": {}
                    }
                },
                "shape_prop": {
                    "transform": {
                        "offset": {
                            "x_in_emu": 0,
                            "y_in_emu": 0
                        }
                    },
                    "geometry": {
                        "type": "prstGeom",
                        "preset_geom": {
                            "shape_type": "rect"
                        }
                    },
                    "non_visual_prop": {
                        "drawing_prop": {
                            "id": relationhipIndex
                        }
                    }
                }
            }
        };
    
        return new Picture(docModel);
    }
}

export default Picture;


