import Document from './Document';
import Body from './Body';
import Paragraph from './Paragraph';
import TextRun from './TextRun';
import Break from './Break';
import Tab from './Tab';
import HyperlinkStart from './HyperlinkStart';
import HyperlinkEnd from './HyperlinkEnd';
import BookmarkStart from './BookmarkStart';
import BookmarkEnd from './BookmarkEnd';
import Table from './Table';
import TableRow from './TableRow';
import TableCell from './TableCell';
import Picture from './Picture';
import HorizontalLine from './HorizontalLine';
import util from '../util/util';

/**
 *     Class.<br/>
 * Document, Body, Paragraph, Run, DrawingObject  .<br/>
 *  {@link Element}  . 
 * @example
 * import Builder from 'path';
 * let builder = new Builder();
 * builder.buildDocumnet();
 */
class Builder {
    constructor() {
        this.lastElementId = 0;
        this.hrParagraphsInfo = [];
        this.hyperlinkMap = {};
    }
    /**
     * Builder Class  .<br/>
     * childrenDocModel, childBuilder  ,<br/>
     * SEModel childBuilder (Child Element) append . 
     * @param {String} SEModel SEModel
     * @param {Array} model docModel
     * @param {childBuilder} model docModel
     * @returns {Document} Document Element
     */
    buildElement(SEModel, children, childBuilder) {
        (children || []).forEach((childModel) => {
            let child = childBuilder.call(this, childModel);
            SEModel.append(child);
            if (!!child && child.getType() === 'HorizontalLine') {
                this.hrParagraphsInfo.push({
                    id: child.parent().element_id,
                    index: Math.max(child.index(), 1)
                });
            }
        });
        return SEModel;
    }

    /**
     *    .<br/>
     *  Document Element ,<br/>
     * buildBody  Document Element body prop Body Element .
     * @param {Object} model docModel
     * @returns {Document} Document Element
     */
    buildDocument(model) {
        let doc = new Document();
        let body = this.buildBody(model.body);
        doc.body = this.splitHrParagraph(body);
        return doc;
    }

    /**
     *  Body Element .<br/>
     *  body children paragraph(docModel) buildPragraph  buildElement ,<br/>
     * Body Element  Paragraph Element  .
     * @param {docModel} model docModel
     * @returns {function} buildElement buildElement
     */
    buildBody(model) {
        let body = new Body(model);
        return this.buildElement(body, model.children, this.buildParagraph);
    }

    /** 
     *  Paragraph Element .<br/>
     *  Paragraph children run(docModel) buildRun  buildElement ,<br/>
     * Paragraph Element  Run Element  .
     * @param {docModel} model docModel
     * @returns {function} buildElement
     */
    buildParagraph(model) {
        if (model.type === 'SECTION') {
            return;
        }

        const runs = util.get(model, ['paragraph', 'children'], []);
        //paragraph bookmark      .
        if (runs.length === 2 &&
            util.get(runs, [0, 'type']) === 'BOOKMARK_START' &&
            util.get(runs, [1, 'type']) === 'BOOKMARK_END') {

            runs.unshift(this.makeEmptyRun());
        }
        if (!model.paragraph.element_id) {
            model.paragraph.element_id = this.getNewElementId();
        }
        let paragraph = new Paragraph(model.paragraph);
        this.setLastElementId(paragraph.getId());

        return this.buildElement(paragraph, model.paragraph.children, this.buildRun);
    }

    /**
     *  Run, DrawingObject Element .<br/>
     *  textRun(docModel)   TextRun Element .<br/>
     * textRun Type  TextRun , DrawingObject Element  .
     * @param {docModel} model docModel
     * @returns {Element} Run|DrawingObject Element
     */
    buildRun(model) {
        let run;
        let textrun = model.textrun;
        switch (model.type) {
            case 'TEXTRUN':
                var type = this.getRunType(textrun);
                switch (type) {
                    case 'text':
                        run = new TextRun(textrun);
                        break;
                    case 'break':
                        run = new Break(textrun);
                        break;
                    case 'tab':
                        run = new Tab(textrun);
                        break;
                    case 'drawing_obj':
                        run = this.buildDrawingObject(textrun);
                        break;
                }
                break;
            case 'HYPERLINK_START':
                run = new HyperlinkStart(model.hyperlink_start);
                this.setHyperlinkStart(run.id, run);
                break;
            case 'HYPERLINK_END':
                run = new HyperlinkEnd(model.hyperlink_end);
                this.setHyperlinkEnd(run.id, run);
                break;
            case 'BOOKMARK_START':
                run = new BookmarkStart(model.bookmark_start);
                break;
            case 'BOOKMARK_END':
                run = new BookmarkEnd(model.bookmark_end);
                break;
        }
        this.setLastElementId(run && run.getId());
        return run;
    }

    /**
     *  DrawingObject Element .<br/>
     *  drawingObject(docModel)  graphicObject  DrawingObejct Element .<br/>
     * DrawingObject  Picture, Table  .
     * @param {docModel} model docModel
     * @returns {Element} Picture|Table Element
     */
    buildDrawingObject(model) {
        const drawingObj = model.drawing_obj;
        const graphicObj = drawingObj.graphic_obj;
        if (!drawingObj.element_id) {
            drawingObj.element_id = this.getNewElementId();
        }
        const mergeDrawingObjProp = function(graphicObject) {
            let copyDrawingObject = {};
            ['element_id', 'layout_prop'].forEach(propName => {
                let prop = drawingObj[propName];
                if(prop) {
                    copyDrawingObject[propName] = prop;
                }
            });
            return Object.assign(copyDrawingObject, graphicObject);
        };

        switch (graphicObj.type) {
            case 'PICTURE':
                let picture = mergeDrawingObjProp(graphicObj.picture);
                picture.textProp = model.prop;
                return new Picture(picture);
            case 'TABLE':
                let table = mergeDrawingObjProp(graphicObj.table);
                return this.buildTable(table);
            case 'HORZONTAL_LINE':
                let horizontalLine = mergeDrawingObjProp(graphicObj.horzontal_line);
                return new HorizontalLine(horizontalLine);
        }
    }

    /**
     *  Table Element .<br/>
     *  table docModel , Table Element . <br/>
     * @param {Object} model table(docModel)
     * @returns {Table} Table Element
     */
    buildTable(model) {
        return this.buildElement(new Table(model), model.table_row, this.buildTableRow);
    }

    /**
     *  TableRow Element .<br/>
     *  tableRow docModel , TableRow Element . <br/>
     * @param {Object} model tableRow(docModel)
     * @returns {TableRow} TableRow Element
     */
    buildTableRow(model) {
        return this.buildElement(new TableRow(model), model.table_cell, this.buildTableCell);
    }

    /**
     *  TableCell Element .<br/>
     *  tableCell docModel , TableCell Element . <br/>
     * @param {Object} model tableCell(docModel)
     * @returns {TableCell} TableCell Element
     */
    buildTableCell(model) {
        let paragraphs = [];
        model.paragraph.forEach(paragraph => {
            paragraphs.push({
                'paragraph': paragraph
            });
        });
        return this.buildElement(new TableCell(model), paragraphs, this.buildParagraph);
    }

    /**
     * docModel TextRun Type  .<br/>
     * textrun text  TextRun, drawing_obj  DrawingObject .
     * @private
     * @param {docModel} model docModel
     * @returns {String} type TextRunType
     */
    getRunType(textrun) {
        let type;
        if (!util.isNil(textrun.text)) {
            type = 'text';
        } else if (!util.isNil(textrun.break)) {
            type = 'break';
        } else if (!util.isNil(textrun.tab)) {
            type = 'tab';
        } else if (!util.isNil(textrun.drawing_obj)) {
            type = 'drawing_obj';
        }
        return type;
    }

    /**
     * Element Id   Id  .
     * @param {Number} id 
     */
    setLastElementId(id) {
        if (!id) {
            return;
        }

        this.lastElementId = Math.max(id, this.lastElementId);
    }

    /**
     *   Element  Id .
     * @returns {Number} last paragraph id
     */
    getNewElementId() {
        return ++this.lastElementId;
    }

    /**
     * Hyperlink  .
     * @returns {Object} hyperlinkMap Object {id : hyperlink model}
     */
    getHyperlinkMap() {
        return this.hyperlinkMap;
    }

    /**
     * id   Hyperlink .
     * @param {Number} id 
     */
    deleteHyperlink(id) {
        delete this.hyperlinkMap[id];
    }

    /**
     * Id  hyperlink .
     * @param {Number} id 
     * @param {HyperlinkStart} hyperlinkStart
     * @param {HyperlinkEnd} hyperlinkEnd
     */
    setHyperlink(id, hyperlinkStart, hyperlinkEnd) {
        this.setHyperlinkStart(id, hyperlinkStart);
        this.setHyperlinkEnd(id, hyperlinkEnd);
    }

    /**
     * Id  hyperlinkStart  .
     * @param {Number} id 
     * @param {HyperlinkStart} hyperlinkStart 
     */
    setHyperlinkStart(id, hyperlinkStart) {
        if (!this.hyperlinkMap[id]) {
            this.hyperlinkMap[id] = {};
        }
        this.hyperlinkMap[id].hyperlinkStart = hyperlinkStart;
    }

    /**
     * Id  hyperlinkEnd  .
     * @param {Number} id 
     * @param {HyperlinkEnd} hyperlinkEnd 
     */
    setHyperlinkEnd(id, hyperlinkEnd) {
        if (!this.hyperlinkMap[id]) {
            this.hyperlinkMap[id] = {};
        }
        this.hyperlinkMap[id].hyperlinkEnd = hyperlinkEnd;
    }

    /**
     *   Paragraph .<br>
     * HTML5 HTML Element Paragraph  '<hr>'     (hr) Paragraph   .
     * @param {Body} body SEModel Element Body
     * @returns {Body} body SEModel Element Body
     */
    splitHrParagraph(body) {
        let hrParagraphsInfo = this.hrParagraphsInfo;
        hrParagraphsInfo.forEach(info => {
            let paragraph = body.findById(info.id);
            if (paragraph.getChildCount() !== 1) {
                let index = info.index;
                let hrParagraph = paragraph.split(index, this.getNewElementId());

                if (hrParagraph.getChildCount() > 1) {
                    hrParagraph.split(1, this.getNewElementId());
                }
            }
        });
        this.hrParagraphsInfo = [];

        return body;
    }

    /**
     * docModel    . 
     * @returns {Object}
     */
    makeEmptyRun() {
        return {
            "type": "TEXTRUN",
            "textrun": {
                "text": ""
            }
        };
    }
}

export default Builder;


