import util from '../util/util';

/**
 *    Class.
 */
class HyperlinkRenderer {
    constructor(renderer) {
        const editor = renderer.editor;
        this.editorId = editor.id;
    }

    /**
     * Hyperlink    hyperlink Element  HTML .
     * @param {Object} run hyperlink Element
     * @returns {String} HTML  
     * @example
     * //return example
     * <a href="https://www.naver.com" style=' '></a>
     */
    getHTML(hyperlink) {
        let prop = util.get(hyperlink, ['prop']);
        let href = this.getHref(prop);
        return `<a hyperlink-id=${hyperlink.id} se-data-href='${href}' target='_blank'></a>`;
    }

    /**
     * hyperlink properties  href .
     * @param {Object} prop 
     * @returns {String} href
     */
    getHref(prop) {
        if(!prop) {
            return;
        }
        let href = prop.target;
        return href;
    }
}

export default HyperlinkRenderer;


