import Element from './Element';
import consts from '../consts/consts';
import util from '../util/util';

/**
 * Paragraph
 * @extends Element
 */
class Paragraph extends Element {
    /**
     *  Paragraph  .
     * @param {Obejct} prop
     * @returns {Paragraph} Paragraph
     */
    static createParagraph(prop) {
        return new Paragraph({
            "prop": prop,
            "children":[]
        });
    }

    /**
     * childrenPropety .
     * @private
     * @returns {String} children
     */
    __getChildrenProperty__() {
        return 'children';
    }

    /**
     * Element Type .
     * @returns {String} Paragraph
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'Paragraph';
    }

    /**
     * Paragraph Element text .
     * @returns {String}
     */
    getText() {
        return this.getChildren().reduce((text, run) => {
            // image/table  
            let addText = run.length() > 0 ? ' ':'';
            if (util.isTextRun(run)) {
                addText = run.getText();
            }
            return text + addText;
        }, '');
    }

    /**
     *  export , SEModel docModel    .<br/>
     * Paragraph Element    ,   Element toPbModel ,<br/>
     *  docModel  JSON Object  .<br/>
     *  docModel  paragraph  JSON Object .
     * @returns {Object} paragraph JSON Object
     */
    toPbModel() {
        let paragraph = {
            "paragraph": super.toPbModel({
                "element_id": this.element_id,
                "prop": this.prop
            }),
            "type": "PARAGRAPH"
        };
        return paragraph;
    }

    /**
     * Paragraph Element text  .
     * @returns {Number} length
     */
    length() {
        return this.children.reduce((sum, run) => {
            return sum + run.length();
        }, 0);
    }

    /**
     * Paragraph Element properties .
     * @returns {Object} prop
     */
    getProp() {
        return this.prop;
    }

    /**
     * Paragraph Element properties  numbering_ref_pr prop  .<br/>
     * numbering_ref_pr properties  True .
     * @returns {Boolean}
     */
    isLists() {
        let prop = this.getProp();
        return util.has(prop, ['numbering_ref_pr']);
    }

    /**
     * Paragraph Element HorizontalLine Paragraph    true .
     * @returns {Boolean}
     */
    isHorizontalLine() {
        return util.isHorizontalLine(this.firstChild());
    }

    /**
     * Paragraph Element Table Paragraph    true .
     * @returns {Boolean}
     */
    isTable() {
        return util.isTable(this.firstChild());
    }

    /**
     * Paragraph Element numbering_id .
     * @param {Number} id 
     */
    setListsId(id) {
        let prop = this.getProp();
        util.set(prop, ['numbering_ref_pr', 'numbering_id'], id);
    }

    /**
     * Paragraph Element properties  numbering_id .<br/>
     * numbering_id  0 .
     * @returns {Number} id
     */
    getListsId() {
        let prop = this.getProp();
        return util.get(prop, ['numbering_ref_pr', 'numbering_id'], 0);
    }

    /**
     * Paragraph Element   level .
     * @param {Number} level 
     */
    setListsLevel(level) {
        let prop = this.getProp();
        util.set(prop, ['numbering_ref_pr', 'level'], level);
    }

    /**
     * Paragraph Element properties  List level .<br/>
     * List level  -1 .
     * @returns {Number} level
     */
    getListsLevel() {
        let prop = this.getProp();
        return util.get(prop, ['numbering_ref_pr', 'level'], -1);
    }

    /**
     * Paragraph Element TextRun .
     * @returns {Array} Array Text Run
     */
    getTextRuns() {
        return this.getChildren().filter(run => util.isTextRun(run));
    }

    /**
     * Paragraph   .
     * @return {*}
     */
    getFirstRun() {
        let run = this.firstChild();

        while (!util.isRun(run)) {
            run = run.nextSibling();
        }

        return run;
    }

    /**
     * Paragraph   .
     * @return {*}
     */
    getLastRun() {
        let run = this.lastChild();

        while (!util.isRun(run)) {
            run = run.previousSibling();
        }

        return run;
    }

    /**
     * 'left', 'right', 'center', 'justify'  type  </br>
     *  align prop  .
     * @param {String} String fontfamily 
     */
    setAlign(type) {
        let prop = this.getProp();
        prop.align = type;
    }

    /**
     * '100', '150' '200'  percent  </br>
     *  spacing prop  .
     *   , sunit    , percent 1000 .
     * @param {Number} Number percent 
     */
    setLineHeight(percent) {
        let prop = this.getProp();
        prop.spacing = {
            'line_in_percent': percent * 1000,
            'line_rule': 'multiple'
        };
    }

    /**
     * 'p', 'h1', 'h2', 'h3'    </br>
     * para prop SynapEditor   'para_style' .
     * @param {Number} Number percent 
     */
    setParaStyle(type) {
        let prop = this.getProp();
        if(type === 'p') {
            delete prop.para_style;
        } else {
            prop.para_style = type;
        }
    }

    /** 
     * px value     spcaing_in_sunit   .
     * @param {Number} Number px 
     */
    setLetterSpacing(value) {
        let prop = this.getProp();
        let endRunProp = util.get(prop, ['end_run_prop']);
        endRunProp.spacing_in_sunit = util.px2sunit(value);
    }

    /**
     * Paragraph Model Table    .
     * @returns {Boolean}
     */
    hasTable() {
        return this.getChildren().some(run => util.isTable(run));
    }

    /**
     * Paragraph Model HorizontalLine    .
     * @returns {Boolean}
     */
    hasHorizontalLine() {
        return this.getChildren().some(run => util.isHorizontalLine(run));
    }

    /**
     * Paragraph Model   .
     * @returns {Boolean}
     */
    isEmpty() {
        return this.getChildren().every(run => run.length() === 0);
    }

    /**
     *   .
     * @returns {Number}
     */
    getIndentLevel() {
        const prop = this.getProp();
        const indent = util.get(prop, ['indent', 'left_in_sunit'], 0);
        const indentStep = consts.INDENT_STEP;
        const level = Math.round(Math.max(indent, 0) / indentStep);
        
        return Math.min(level, 8);
    }

    /**
     *   .
     * @param {Number} sunit 
     */
    setIndent(sunit) {
        const prop = this.getProp();
        util.set(prop, ['indent', 'left_in_sunit'], sunit);
    }
}

export default Paragraph;


