import util from '../util/util';

/**
 *     RelationshipListManager Class.
 * @class RelationshipListManager
 */
class RelationshipListManager {
    /**
     * RelationshipListManager  .
     */
    constructor() {
        this.list = null;
    }

    /**
     * relationshipList .
     * @param {Array} list 
     */
    setList(list) {
        this.list = list || [];
    }

    /**
     * relationshipList .
     * @returns {Array}
     */
    getList() {
        return this.list;
    }

    /**
     * relationship  .
     * @param {Object} item 
     */
    appendItem(item) {
        this.list.push(item);
    }

    /**
     * relationshipid   .
     * @param {String} rId 
     * @returns {Object}
     */
    getItem(rId) {
        return this.list.find((item) => {
            return item.id === rId;
        });
    }

    /**
     * relationshipId    .
     * @param {String} rId 
     * @returns {String}
     */
    getImagePath(rId) {
        let item = this.list.find((item) => {
            return item.id === rId;
        });

        return util.get(item, ['target']);
    }

    /**
     *  relationship    , 
     *   relationship  .
     * @param {String} url 
     * @param {String} type
     * @param {String} [targetMode='Internal'] 
     * @param {String} method
     * @returns {Number}
     */
    createItem(url, type, targetMode = 'Internal', method) {
        const idx = this.getNewRelationShipIdx();
        const relationshipItem = {
            id: 'rId' + idx,
            target: url,
            target_mode: targetMode,
            type: type
        };

        if (method) {
            relationshipItem['method'] = method;
        }

        this.appendItem(relationshipItem);

        return idx;
    }

    /**
     *  relactionship  .
     * @returns {Number}
     */
    getNewRelationShipIdx() {
        let length = this.list.length;
        return ++length;
    }

    /**
     * RelationShipItem uploadPath .
     * @param {Number} rId 
     * @param {String} uploadPath 
     */
    setRelationShipItemPath(relationshipItem, uploadPath) {
        util.set(relationshipItem, ['target'], uploadPath);
    }

    /**
     * RelationShipItem method .
     * @param {Object} relationshipItem 
     * @param {String} [method = ''] 
     */
    setRelationShipItemMethod(relationshipItem, method = '') {
        util.set(relationshipItem, ['method'], method);
    }
}

export default RelationshipListManager;


