import Element from './Element';
import consts from '../consts/consts';
import util from '../util/util';

/**
 * TextRun
 * @extends Element
 */
class TextRun extends Element {
    /**
     *  Text Run  .
     * @returns {TextRun} Text Run
     */
    static createTextRun(prop) {
        return new TextRun({
            text: '',
            prop: prop || {"fonts":{"hint":"eastAsia"}}
        });
    }

    /**
     * Element Type .
     * @returns {String} TextRun
     */
    getType() {
        // Type Obejct.consturctor  minify/uglify   .
        return 'TextRun';
    }

    /**
     *  export , SEModel docModel    .<br/>
     * docModel  textrun  JSON Object .
     * @returns {Object} textrun JSON Object
     */
    toPbModel() {
        let textrun = {
            "textrun": {
                "text": this.text,
                "prop": this.prop
            },
            "type": "TEXTRUN"
        };
        return textrun;
    }

    /**
     * index   TextRun Element cloned   .</br>
     * TextRun Element text index  , cloned TextRun Element  text .</br>
     *  cloned TextRun Element TextRun Element(this) insert .
     * @private
     * @param {Number} index 
     * @returns {Run} Run Element 
     * @example
     * ""(TextRun).split(2)
     * this.text = ''
     * cloned.text = ''
     */
    split(index) {
        let cloned = this.clone();
        let text = this.text;
        this.text = text.substring(0, index);
        cloned.text = text.substring(index);
        cloned.insertAfter(this);
        return cloned;
    }

    /**
     * TextRun Element properties .
     * @returns {Object} - TextRun Element Properties
     */
    getProp() {
        return util.get(this, ['prop']) ? util.get(this, ['prop']) : this.prop = {};
    }

    /**
     * TextRun Element text  .
     * @returns {String} - TextRun Element text
     */
    getText() {
        return util.get(this, ['text'], '');
    }

    /**
     * TextRun Element text  .
     * @returns {Number} - TextRun Element text length
     */
    length() {
        return this.getText().length;
    }

    /**
     * TextRun Element text  0 .
     * @returns {Boolean} - text  0  true
     */
    isEmpty() {
        return this.length() === 0;
    }

    /**
     * {'r': 255, 'g': 255, 'b': 255}   RGB  value  </br>
     *  fill prop  .
     * @param {String} - hex value 
     */
    setFill(value) {
        let prop = this.getProp();
        prop.fill = {
            'solid_fill': {
                'color': {
                    'srgb_color': {
                        'value_in_hex': util.hexToDecimal(value)
                    },
                    'type': 'SRGB'
                }
            },
            'type': 'SOLID_FILL'
        };
    }

    /**
     * {'r': 255, 'g': 255, 'b': 255}   RGB  value  </br>
     *  textfill prop  .
     * @param {String} - hex value 
     */
    setTextFill(value) {
        let prop = this.getProp();
        if (value instanceof Function) {
            value = value(prop[actionName]);
        }

        prop.text_fill = {
            'solid_fill': {
                'color': {
                    'srgb_color': {
                        'value_in_hex': util.hexToDecimal(value)
                    },
                    'type': 'SRGB'
                }
            },
            'type': 'SOLID_FILL'
        };
    }

    /**
     * '', ''   fontfamily value  </br>
     *  textfill prop  .
     * @param {String} String fontfamily 
     */
    setTextFont(value) {
        let prop = this.getProp();
        prop.fonts = {
            'ascii': value
        };
    }

    /**
     * FONT_SIZE_STEP  fontSize prop   fontsize prop  . 
     */
    increaseFontSize() {
        let prop = this.getProp();
        let currentSize = util.get(prop, ['font_size_in_sunit'], consts.DEFAULT_FONT_SIZE);
        let fontSize = consts.FONT_SIZE_STEP.find(size => size > currentSize) || currentSize;
        prop.font_size_in_sunit = fontSize;
    }

    /**
     * FONT_SIZE_STEP  fontSize prop   fontsize prop  . 
     */
    decreaseFontSize(value) {
        let prop = this.getProp();
        let currentSize = util.get(prop, ['font_size_in_sunit'], consts.DEFAULT_FONT_SIZE);
        let fontSize = consts.FONT_SIZE_STEP.slice(0).reverse().find(sz => sz < currentSize) || currentSize;
        prop.font_size_in_sunit = fontSize;
    }

    /**
     * sunit fontsize    fontsize prop  .
     * @param {Number} Number sunit 
     */
    setFontSize(value) {
        let prop = this.getProp();
        prop.font_size_in_sunit = value;
    }

    /**
     * Boolean    bold prop  .
     * @param {Boolean} value 
     */
    setBold(value) {
        let prop = this.getProp();
        prop.bold = value ? 1 : 0;;
    }

    /**
     * Boolean  </br>
     *  italic prop  .
     * @param {Boolean} value 
     */
    setItalic(value) {
        let prop = this.getProp();
        prop.italic = value ? 1 : 0;
    }

    /**
     * Boolean    underline prop  .
     * @param {Boolean} value 
     */
    setUnderline(value) {
        let prop = this.getProp();
        prop.underline = {
            'style': value ? 'single' : 'none'
        };
    }

    /**
     * Boolean    strike prop  .
     * @param {Boolean} value 
     */
    setStrike(value) {
        let prop = this.getProp();
        prop.strike = {
            'style': value ? 'single' : 'none'
        };
    }
}

export default TextRun;


