import $ from 'jquery';

/**
 *  Dialog  .
 * Dialog    Dialog  
 *      .
 * @class Dialog
 */
class Dialog {
    /**
     * Dialog  .
     * @param {String} editorId -     
     */
    constructor(editorId) {
        this.editorId = editorId;
        this.$element = null;
    }

    /**
     * Dialog Container  Dialog Dom  .
     * @private
     */
    __createDom__() {
        //Dialog       .
    }

    /**
     *     .
     */
    confirm() {
        this.hide();
    }

    /**
     *     .
     */
    cancel() {
        this.hide();
    }

    /**
     * Dialog Element  .
     * @param {Object} position
     */
    setPosition(position) {
        this.$element.css(position);
    }

    /**
     * Dialog Element .
     */
    show() {
        const $editor = $('#' + this.editorId);
        const $dialogContainer = $editor.find('.se-dialog-container');
        const $main = $editor.find('.se-main');
        const $toolbar = $editor.find('.se-toolbar');
        const position = {
            top: $toolbar.get(0).offsetHeight - $dialogContainer.get(0).offsetTop,
            left: ($main.get(0).offsetWidth - this.$element.get(0).offsetWidth) / 2 - $dialogContainer.get(0).offsetLeft
        };

        this.setPosition(position);
        this.$element.addClass('show');
    }

    /**
     * Dialog Element .
     */
    hide() {
        this.$element.removeClass('show');
    }
}

export default Dialog;


