import IndexManager from './IndexManager';
import util from '../util/util';

/**
 *   
 *  id    ,        .
 * indexManager    .     indexManager .
 * A B  , B indexManager    A indexManager .
 * A B  , B   indexManager  .
 *  B        B  indexManager  ,   B .
 *      ,     .
 *  index.get()     ,         .
 */

function search(element, fn, backward) {
    if (!element) {
        return null;
    }
    let ret;
    let childElem = backward ? element.lastChild() : element.firstChild();
    while (!ret && childElem) {
        ret = fn.call(childElem, childElem) ? childElem : search(childElem, fn, backward);
        childElem = backward ? element.previousSibling() : childElem.nextSibling();
    }
    return ret || null;
}

function traverse(element, fn) {
    if (!element) {
        return;
    }
    let childElem = element.firstChild();
    while (childElem) {
        fn.call(childElem, childElem);
        traverse(childElem, fn);
        childElem = childElem.nextSibling();
    }
}

function truthy(elem, condition) {
    return condition && (
        condition === elem ||
        (condition.prototype instanceof Element ?
            elem instanceof condition :
            (condition instanceof Function && condition.call(elem, elem))
        )
    );
}

function firstOf(arr, condition) {
    let first;
    arr.every(function (elem) {
        if (truthy(elem, condition)) {
            first = elem;
            return true;
        }
        return false;
    });
    return first;
}

function clone(obj, deep, newId) {
    let props = Object.keys(obj);
    let children = obj instanceof Element ? obj.getChildren() : [];
    let clonedProps = {};
    let clonedChildren = [];
    for (let k in props) {
        let prop = props[k];
        // array.protytype vmParser  , for in    .
        if (!props.hasOwnProperty(k) || prop.indexOf('__') === 0) {
            continue;
        }
        let o = obj[prop];
        if (o instanceof Object) {
            if (children === o) {
                clonedProps[prop] = [];
                if (deep) {
                    for (let i = 0; i < children.length; i++) {
                        clonedChildren.push(clone(children[i], true));
                    }
                }
            } else {
                clonedProps[prop] = clone(o, true);
            }
        } else {
            clonedProps[prop] = o;
        }
    }
    if (clonedProps.element_id && typeof newId !== 'undefined') {
        clonedProps.element_id = newId;
    }

    if (obj instanceof Element) {
        let clonedElement = new obj.constructor(clonedProps);
        clonedChildren.forEach(function (cc) {
            clonedElement.append(cc);
        });
        return clonedElement;
    } else {
        return clonedProps;
    }
}

function attach(elem, parent, attachFunction) {
    if (!parent) {
        return;
    }
    let prevParent = elem.__parent__;
    if (prevParent) {
        prevParent.getChildren().splice(elem.index(), 1);
    }
    elem.__parent__ = parent;
    if (elem.__indexManager__) {
        parent.__getIndexManager__().mergeWith(elem.__indexManager__);
        elem.__indexManager__ = null; //  indexMananger 
    }
    if (attachFunction) {
        attachFunction();
    }
    if (prevParent) {
        elem.__notifyMoved__(elem, prevParent, prevParent.root() !== parent.root());
    } else {
        elem.__notifyAttached__(elem);
    }
}

function detach(elem, detachFunction) {
    // TODO:  ?
    let parent = elem.__parent__;
    if (!parent) {
        return;
    }
    let i = elem.index();
    elem.__parent__ = null;
    if (detachFunction && i > -1) {
        detachFunction(parent, i);
    }
    elem.__notifyDetached__(elem, parent);
}
/**
 *   ,    Class.<br/>
 *  , ,      API 
 */
class Element {
    constructor(model) {
        model = util.extend(this.__default__(), model || {});
        let childrenProperty = this.__getChildrenProperty__();
        Object.keys(model).forEach(function (k) {
            if (this[k]) {
                console.warn('"model.%s" is hidden by "%s.%s" %s.',
                    k,
                    this,
                    k,
                    this[k] instanceof Function ? 'method' : 'property');
            } else if (this.__includesProperty__(k) && k !== childrenProperty) {
                this[k] = model[k];
            }
        }.bind(this));
        this.__parent__ = null;
        this.__indexManager__ = null;
        if (childrenProperty) {
            this[childrenProperty] = [];
        }
    }

    __default__() {
        return {};
    }

    __includesProperty__(propName) {
        return true;
    }

    __getIndexManager__() {
        if (this.parent()) {
            return this.parent().__getIndexManager__();
        }
        if (!this.__indexManager__) {
            this.__indexManager__ = new IndexManager();
            this.each(descendant => {
                descendant.__addToIndex__();
            });
        }
        return this.__indexManager__;
    }

    __getIdPropName__() {
        return 'element_id';
    }

    /**
     * Element id .
     * @returns {Number} id
     */
    getId() {
        return this[this.__getIdPropName__()];
    }

    setId(id) {
        let prevId = this.getId();
        this[this.__getIdPropName__()] = id;

        let index = this.__getIndex__();
        if (index) {
            index.set(id, this);
            //   Position          .
            // if (id !== prevId) {
            //     index.remove(prevId, this);
            // }
        }
        return this;
    }

    __extend__(a, b) {
        return util.extend(a, b);
    }

    // attachedElement     .
    __notifyAttached__(attachedElement) {
        this.__addToIndex__();
        this.onAttached(attachedElement);
        this.getChildren().forEach(function (child) {
            child.__notifyAttached__(attachedElement);
        });
    }

    // detachedElement     .
    __notifyDetached__(detachedElement, prevParent) {
        this.onDetached(detachedElement, prevParent);
        this.getChildren().forEach(function (child) {
            child.__notifyDetached__(detachedElement, prevParent);
        });
    }

    // movedElement     .
    __notifyMoved__(movedElement, prevParent, rootChanged) {
        if (rootChanged) {
            this.__addToIndex__();
        }
        this.onMoved(movedElement, prevParent);
        this.getChildren().forEach(function (child) {
            child.__notifyMoved__(movedElement, prevParent, rootChanged);
        });
    }

    __getIndex__() {
        if (typeof this.getId() === 'undefined') {
            return null;
        }
        return this.__getIndexManager__().get('element');
    }

    __addToIndex__() {
        let index = this.__getIndex__();
        if (index) {
            index.set(this.getId(), this);
        }
    }

    onAttached(attachedElement) {}

    onDetached(detachedElement, prevParent) {}

    onMoved(movedElement, prevParent) {}

    toJSONString() {
        let self = this;
        let obj = {};
        return '{' + Object.keys(this).filter(function (propName) {
            return !(propName instanceof Function) && propName.indexOf('__') !== 0;
        }).map(function (propName) {
            let prop = self[propName];
            let str = '"' + propName + '":';
            if (prop === self.getChildren()) {
                str += '[' + self.getChildren().map(function (child) {
                    return child.toJSONString();
                }).join(',') + ']';
            } else {
                str += JSON.stringify(prop);
            }
            return str;
        }).join(',') + '}';
    }

    /**
     *  export , SEModel docModel    .<br/>
     *       toPbModel .
     * @param {Object} SEModel Element
     * @returns {Object} docModel JSON Object
     */
    toPbModel(model = {}) {
        if (this.children && this.children.length > 0) {
            model.children = this.getChildren().map(child => child.toPbModel());
        };
        return model;
    }

    __getChildrenProperty__() {
        return null;
    }

    getChildren() {
        let childrenProperty = this.__getChildrenProperty__();
        return childrenProperty === null ? [] : this[childrenProperty];
    }

    length() {
        return 0;
    }

    isRoot() {
        return !this.__parent__;
    }

    // final
    parent() {
        return this.__parent__;
    }

    ancestors() {
        return this.ancestorsUntil();
    }

    ancestorsUntil(condition) {
        let anscestor = this.parent();
        let arr = [];
        while (anscestor && !truthy(anscestor, condition)) {
            arr.push(anscestor);
            anscestor = anscestor.parent();
        }
        return arr;
    }

    // final
    closest(condition) {
        let elem = this;
        while (elem && !truthy(elem, condition)) {
            elem = elem.parent();
        }
        return elem || null;
    }

    // final
    root() {
        let elem = this;
        while (elem.parent()) {
            elem = elem.parent();
        }
        return elem;
    }

    closestCommonAncestorOf(element) {
        let thisAnscestors = this.ancestors().reverse();
        let anotherAnscestors = element.ancestors().reverse();
        let i;
        for (i = 0; thisAnscestors[i] && thisAnscestors[i] === anotherAnscestors[i]; i++) {
            // pass common ancestors
        }
        return thisAnscestors[i - 1];
    }

    // final
    index() {
        return this.siblings().indexOf(this);
    }

    /**
     * Element   .
     * @returns {Object} Element
     */
    nextSibling() {
        return this.siblings()[this.index() + 1] || null;
    }

    /**
     * Element   .
     * @returns {Object} Element
     */
    previousSibling() {
        return this.siblings()[this.index() - 1] || null;
    }

    /**
     * Element    .
     * @returns {Array} Element
     */
    siblings() {
        let parent = this.parent();
        return parent ? parent.getChildren() : [];
    }

    /**
     * Element   .
     * @returns {Array} Element
     */
    nextSiblings() {
        return this.siblings().slice(this.index() + 1);
    }

    /**
     * Element   .
     * @returns {Array} Element
     */
    previousSiblings() {
        return this.siblings().slice(0, this.index());
    }

    /**
     * Element   .
     * @returns {Object} Element
     */
    firstChild(condition) {
        if (typeof condition !== 'undefined') {
            return firstOf(this.getChildren(), condition);
        } else {
            return this.getChildren()[0];
        }
    }

    /**
     * Element   .
     * @returns {Object} Element
     */
    lastChild(condition) {
        let children = this.getChildren();
        if (typeof condition !== 'undefined') {
            return firstOf(children.slice(0).reverse(), condition);
        } else {
            return children[children.length - 1];
        }
    }

    /**
     * Element n  .
     * @returns {Object} Element
     */
    nthChild(n) {
        return this.getChildren()[n];
    }

    /**
     * Element   .
     * @returns {Number} length
     */
    getChildCount() {
        return this.getChildren().length;
    }

    each(fn) {
        traverse(this, fn);
        return this;
    }

    findAll(fn) {
        let arr = [];
        traverse(this, function () {
            if (fn.call(this, this)) {
                arr.push(this);
            }
        });
        return arr;
    }

    findFirst(fn) {
        return search(this, fn) || null;
    }

    findLast(fn) {
        return search(this, fn, true) || null;
    }

    // final
    findById(id) {
        return this.__getIndexManager__().get('element').get(id, this) || null;
    }

    //  element this  (   )
    // final
    contains(element) {
        while (element) {
            element = element.parent();
            if (element === this) {
                return true;
            }
        }
        return false;
    }

    // final
    compare(anotherElement) {
        if (this === anotherElement) {
            return 0;
        }
        let thisAnscestors = this.ancestors().reverse();
        let anotherAnscestors = anotherElement.ancestors().reverse();
        let i = 0;
        for (; thisAnscestors[i] && thisAnscestors[i] === anotherAnscestors[i]; i++) {
            // pass common ancestors
        }
        let i1 = (thisAnscestors[i] || this).index();
        let i2 = (anotherAnscestors[i] || anotherElement).index();
        if (i1 === -1 || i2 === -1) {
            return NaN;
        }
        return (i1 - i2) / Math.abs(i1 - i2);
    }

    appendTo(element) {
        attach(this, element, () => element.getChildren().push(this));
        return this;
    }

    prependTo(element) {
        attach(this, element, () => element.getChildren().unshift(this));
        return this;
    }

    // final
    append(element) {
        if (element instanceof Element) {
            element.appendTo(this);
        }
        return this;
    }

    // final
    prepend(element) {
        if (element instanceof Element) {
            element.prependTo(this);
        }
        return this;
    }

    insertBefore(element) {
        attach(this, element.parent(), () => element.siblings().splice(element.index(), 0, this));
        return this;
    }

    insertAfter(element) {
        attach(this, element.parent(), () => element.siblings().splice(element.index() + 1, 0, this));
        return this;
    }

    // final
    before(element) {
        if (element instanceof Element) {
            element.insertBefore(this);
        }
        return this;
    }

    // final
    after(element) {
        if (element instanceof Element) {
            element.insertAfter(this);
        }
        return this;
    }

    replaceWith(element) {
        let parent = this.parent();
        detach(this, (prevParent, i) => {
            prevParent.getChildren()[i] = element;
            attach(element, parent);
        });
        return this;
    }

    // final
    remove() {
        detach(this, (prevParent, i) => prevParent.getChildren().splice(i, 1));
        return this;
    }

    // final
    empty() {
        let children = this.getChildren();
        children.forEach(child => detach(child));
        children.splice(0, children.length);
        return this;
    }

    getText() {
        return '';
    }

    clone(deep, newId) {
        return clone(this, deep, newId);
    }

    split(index, newId) {
        let cloned = this.clone(false, newId);
        let children = this.getChildren();
        children.slice(index, children.length).forEach(function (rightChild) {
            cloned.append(rightChild);
        });
        cloned.insertAfter(this);
        return cloned;
    }

    // endChildIndex: exclusive
    removeChild(startChildIndex, endChildIndex) {
        let children = this.getChildren();
        if (typeof endChildIndex === 'undefined') {
            endChildIndex = children.length;
        }
        let childrenToRemove = children.splice(startChildIndex, endChildIndex - startChildIndex);
        childrenToRemove.slice(0).forEach(child => detach(child));
        return this;
    }

    /**
     * Run Start Offset .<br>
     * Run  0 .
     * @returns {Number} start Offset
     */
    getRunStartOffset() {
        if (!util.isRun(this)) {
            return 0;
        }
        return this.previousSiblings().reduce((sum, prev) => sum + prev.length(), 0);
    }

    /**
     * Run End Offset .
     * Run  0 .
     * @returns {Number} end Offset
     */
    getRunEndOffset() {
        if (!util.isRun(this)) {
            return 0;
        }
        return this.getRunStartOffset() + this.length();
    }
}

export default Element;


