import '../../lang/ko';
import '../../lang/en';

import $ from 'jquery';
import vmManager from './modules/vm/vmManager';
import Controller from './controller/Controller';
import PositionManager from './manager/PositionManager';
import SelectionManager from './manager/SelectionManager';
import Ime from './Ime';
import SelectionRenderer from './renderer/SelectionRenderer';
import MouseEventManager from './manager/MouseEventManager';
import SEModelManager from './manager/SEModelManager';
import SEClipboard from './modules/SEClipboard';
import ModelHandler from './model/ModelHandler';
import ListsManager from './manager/ListsManager';
import RelationshipListManager from './manager/RelationshipListManager';
import ThemeListManager from './manager/ThemeListManager';
import DocumentStyleManager from './manager/DocumentStyleManager';
import HTMLToModel from './modules/convert/HTMLToModel';
import HTMLToModelForCK from './modules/convert/HTMLToModelForCK';
import Renderer from './renderer/Renderer';
import RenderUtil from './util/RenderUtil';
import util from './util/util';
import UIManager from './manager/UIManager';
import defaultModel from './defaultModel';
import defaultConfiguration from './defaultConfiguration';
import localeManager from './localeManager';
import WindowEventManager from './manager/WindowEventManager';
import UndoRedo from './modules/UndoRedo';

import '../css/style.css';
import '../css/renderStyle.css';

/**
 *  .
 * @example
 * // html
 * <div id="editor"></div>
 * 
 * // javascript
 * var configuration = {
 *     toolbar: ['bold', 'italic', 'underline', 'strike', '|', 'image'],
 *     language: 'en'
 * };
 * 
 * var editor = new SynapEditor('editor', configuration);
 */
class SynapEditor {
    /**
     * @param {String} editorId   DIV Element id
     * @param {Object} configuration  
     * @param {Array} serializedPbData 
     * @param {String} importPath
     */
    constructor(editorId, configuration, serializedPbData, importPath) {
        this.id = editorId;
        this.configuration = mergeConfiguration(configuration);

        this.info = this.createEditorInfo(editorId, this.configuration);
        this.controller = new Controller(this);

        this.uiManager = new UIManager(this.id, this.controller);
        this.create(editorId, this.configuration);

        this.listsManager = new ListsManager();
        this.documentStyleManager = new DocumentStyleManager();
        this.relationshipListManager = new RelationshipListManager();
        this.themeListManager = new ThemeListManager();
        this.SEModelManager = new SEModelManager();

        this.positionManager = new PositionManager(this.SEModelManager);
        this.selectionManager = new SelectionManager(this);

        this.ime = new Ime(editorId, this.controller);
        this.selectionRenderer = new SelectionRenderer(editorId, this.selectionManager, this.SEModelManager, this.ime, this.uiManager);
        this.mouseEventManager = new MouseEventManager(editorId, this.controller, this.selectionManager);

        this.modelHandler = new ModelHandler(this);
        this.htmlToModel = new HTMLToModel();
        this.SEClipboard = new SEClipboard(this);
        this.renderUtil = new RenderUtil(this.themeListManager);
        this.renderer = new Renderer(this);

        this.windowEventManager = new WindowEventManager(editorId, this.ime, this.controller);
        this.undoRedo = new UndoRedo(editorId);

        this.initSEModel(serializedPbData, importPath);

        return this;
    }

    create(id, configuration) {
        let $editorRoot = $(`#${id}`);
        if ($editorRoot.length < 1 || $editorRoot.find('.se-main').length > 0) {
            // fail
            return;
        }

        let $wrapper = $('<div class="se">');
        let $toolbarWrapper = $('<div class="se-toolbar">');
        let $main = $('<div class="se-main"><div class="se-contents">');
        let $popupContainer = $('<div class="se-popup-container">');
        let $dialogContainer = $('<div class="se-dialog-container">');

        if (!!configuration) {
            if (!!configuration.width) {
                $editorRoot.width(configuration.width);
            }
            if (!!configuration.height) {
                $main.height(configuration.height);
            }
        }

        $wrapper.append($toolbarWrapper);
        $wrapper.append($main);
        $wrapper.append($popupContainer);
        $wrapper.append($dialogContainer);
        
        $editorRoot.html($wrapper);

        this.uiManager.init(configuration);
    }

    /**
     *    .
     * @param {Array|JSON} modelData - serialized  pbData  json  docModel
     * @param {String} importPath
     * @param {String} publishingData 
     */
    initSEModel(modelData, importPath, publishingData) {
        let docModel;

        if (publishingData) {
            const tempDocument = document.implementation.createHTMLDocument('');
            tempDocument.documentElement.innerHTML = publishingData;

            const html = tempDocument.getElementsByClassName('se-contents')[0].innerHTML;
            const style = tempDocument.getElementsByTagName('style')[0];
            const cssRules = !!style ? style.sheet.cssRules : undefined;

            docModel = this.htmlToModel.convertToDocument(html, cssRules);
        } else {
            modelData = modelData || defaultModel;
         
            if (Array.isArray(modelData)) {
                docModel = vmManager.parse(modelData); 
            } else {
                docModel = modelData;
            }
        }
        
        docModel = recalculateListsId(docModel); //   .

        window.docModel = docModel; // 
        this.setEditorInfo('importPath', importPath);

        this.SEModelManager.setSEModel(docModel);
        this.documentStyleManager.setDocumentStyles(util.get(docModel, ['document_styles']));
        this.relationshipListManager.setList(util.get(docModel, ['relationship_list', 'relationship']));
        this.themeListManager.setList(util.get(docModel, ['theme_list', 'theme']));
        this.listsManager.setLists(util.get(docModel, ['numbering_list', 'numbering']));
        this.listsManager.setPictureLists(util.get(docModel, ['numbering_list', 'picture_bullet_numbering']));

        this.renderer.renderAll();

        this.selectionManager.refresh();
        this.selectionRenderer.renderSelection(this.selectionManager.selection);

        this.ime.focus();
    }

    /**
     *   .
     *     .
     * @private
     * @param {String} editorId 
     * @param {Object} configuration  
     */
    createEditorInfo(editorId, configuration) {
        if (!window.SE_INFO) {
            window.SE_INFO = {};
        }

        const defaultLang = configuration.defaultLanguage;
        const lang = configuration.language;

        return window.SE_INFO[editorId] = {
            language: localeManager.getLanguage(defaultLang, lang)
        };
    }

    /**
     *   .
     * @param {String} key 
     * @param {*} value 
     */
    setEditorInfo(key, value) {
        window.SE_INFO[this.id][key] = value;
    }

    /**
     *   
     * @param {String} [key] 
     * @returns {*} key   key  , key     info  
     */
    getEditorInfo(key) {
        return !!key ? window.SE_INFO[this.id][key] : window.SE_INFO[this.id];
    }

    /**
     * SEModelManager  .
     * @returns {SEModelManager}
     */
    getSEModelManager() {
        return this.SEModelManager;
    }

    /**
     * DocumentStyleManager  .
     * @returns {DocumentStyleManager}
     */
    getDocumentStyleManager() {
        return this.documentStyleManager;
    }

    /**
     * RelationshipListManger  .
     * @returns {RelationshipListManger}
     */
    getRelationshipListManager() {
        return this.relationshipListManager;
    }

    /**
     * ThemeListManger  .
     * @returns {ThemeListManger}
     */
    getThemeListManager() {
        return this.themeListManager;
    }

    /**
     * ListsManager  .
     * @returns {ListsManager}
     */
    getListsManager() {
        return this.listsManager;
    }

    /**
     * Renderer  .
     * @returns {Renderer}
     */
    getRenderer() {
        return this.renderer;
    }

    /**
     * RenderUtil  .
     * @returns {RenderUtil}
     */
    getRenderUtil() {
        return this.renderUtil;
    }

    /**
     * SelectionRenderer  .
     * @returns {SelectionRenderer}
     */
    getSelectionRenderer() {
        return this.selectionRenderer;
    }

    /**
     * SelectionManager  .
     * @returns {SelectionManager}
     */
    getSelectionManager() {
        return this.selectionManager;
    }

    /**
     * PositionManager  .
     * @returns {PositionManager}
     */
    getPositionManager() {
        return this.positionManager;
    }

    /**
     * UIManager  .
     * @returns {UIManager}
     */
    getUIManager() {
        return this.uiManager;
    }

    /**
     * Ime  .
     * @returns {Ime}
     */
    getIme() {
        return this.ime;
    }

    /**
     * SEClipboard  .
     * @returns {SEClipboard}
     */
    getSEClipboard() {
        return this.SEClipboard;
    }

    /**
     * Caret  .
     * @returns {Caret}
     */
    getCaret() {
        return this.caret;
    }

    /**
     * MouseEventManager  .
     * @returns {MouseEventManager}
     */
    getMouseEventManager() {
        return this.mouseEventManager;
    }

    /**
     * Configuration  .
     * @returns {Configuration}
     */
    getConfiguration() {
        return this.configuration;
    }

    /**
     * ModelHandler  .
     * @returns {ModelHandler}
     */
    getModelHandler() {
        return this.modelHandler;
    }

    /**
     * UndoRedo  .
     * @returns {UndoRedo}
     */
    getUndoRedo() {
        return this.undoRedo;
    }

    /**
     * Controller  .
     */
    getController() {
        return this.controller;
    }

    /**
     *   HTML .
     * @returns {String}
     */
    getPublishingHtml() {
        const renderCss = $('.synap-css').get(1).textContent;

        //EDITOR-638
        //IE     CSS content  'A' 'a'       .
        //      ,     .
        //      ,  .
        const listsCss = $(`.se-lists-css[data-editor-id=${this.id}]`).get(0).textContent.replace(/\u200b/gi, "");

        const $SEContents = $(`#${this.id}`).find('.se-contents').clone();
        const $linkTags = $SEContents.find('a');

        $linkTags.attr('href', index => {
            return $($linkTags.get(index)).attr('se-data-href');
        });

        return `
            <html>
                <style>
                    ${renderCss}${listsCss}
                </style>
                <body>
                    <div class="se-contents">
                        ${$SEContents.html()}
                    </div>
                </body>
            </html>
        `;
    }
}

/**
 *    .
 * docModel SEModel    .
 * @param {Object} docModel -   
 * @returns {Object} - docModel
 */
function recalculateListsId(docModel) {
    let lastListsId = 0;
    const paragraphs = [];
    const makeNewListsId = function () {
        return ++lastListsId;
    };

    //docModel paragraph model .
    util.get(docModel, ['body', 'children'], []).forEach(child => {
        if (child.type === 'PARAGRAPH') {
            paragraphs.push(util.get(child, ['paragraph']));
        }
    });

    //    .
    paragraphs.forEach(paragraph => {
        const listsId = util.get(paragraph, ['prop', 'numbering_ref_pr', 'numbering_id'], 0);
        lastListsId = Math.max(listsId, lastListsId);
    });

    //   .
    for (let i = 0, n = paragraphs.length - 1; i < n; i++) {
        const currParagraph = paragraphs[i];
        const nextParagraph = paragraphs[i + 1];

        const currListsId = util.get(currParagraph, ['prop', 'numbering_ref_pr', 'numbering_id'], 0);
        const nextListsId = util.get(nextParagraph, ['prop', 'numbering_ref_pr', 'numbering_id'], 0);

        // 1.           
        // 2.      (nextListsId === 0)
        //    , 
        //            .
        if (currListsId && currListsId !== nextListsId) {
            const newListsId = makeNewListsId();
            //update paragraph model 
            for (let j = i + 1, m = paragraphs.length; j < m; j++) {
                if (currListsId === util.get(paragraphs[j], ['prop', 'numbering_ref_pr', 'numbering_id'], 0)) {
                    util.set(paragraphs[j], ['prop', 'numbering_ref_pr', 'numbering_id'], newListsId);
                }
            }

            //update numbering_list
            const numberingItem = util.get(docModel, ['numbering_list', 'numbering'], []).find(item => {
                return item.numbering_id === currListsId;
            });

            const copiedItem = util.cloneDeep(numberingItem);
            util.set(copiedItem, ['numbering_id'], newListsId);
            util.get(docModel, ['numbering_list', 'numbering'], []).push(copiedItem);
        }
    }

    return docModel;
}

/**
 *        .
 * @param {Object} configuration   
 * @returns {Object}       
 */
function mergeConfiguration(configuration) {
    const mergedConfiguration = Object.assign({}, configuration);
    const configKeys = Object.keys(defaultConfiguration);

    configKeys.forEach((key) => {
        if (!mergedConfiguration[key]) {
            mergedConfiguration[key] = defaultConfiguration[key];
        }
    });

    return mergedConfiguration;
}

window.jQuery = $;
window.SynapEditor = SynapEditor;
window.HTMLToModelForCK = HTMLToModelForCK; // 
export default SynapEditor;


