/**
 * .
 * @static
 * @namespace util
 */
const ModelUtil = {
    /**
     *   Element Paragraph .
     * Paragraph Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isParagraph: function (element) {
        return !!element && element.getType() === 'Paragraph';
    },

    /**
     *   Element Run .
     * Run level Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean}
     */
    isRun: function (element) {
        return this.isTextRun(element) || this.isTable(element) || this.isPicture(element) || this.isHyperlink(element);
    },

    /**
     *   Element TextRun .
     * TextRun Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isTextRun: function (element) {
        return !!element && element.getType() === 'TextRun';
    },

    /**
     * Element line break  .</br>
     * line break true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean}
     */
    isLineBreak: function (element) {
        return this.isTextRun(element) && !!element.break && element.break.type === 'line';
    },

    /**
     * Element Tab  .</br>
     * Tab true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean}
     */
    isTab: function(element) {
        return this.isTextRun(element) && !!element.tab;
    },

    /**
     *   Element Hyperlink .
     * Hyperlink Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isHyperlink: function (element) {
        return !!element && (element.getType() === 'HyperlinkStart' || element.getType() === 'HyperlinkEnd');
    },

    /**
     * Element Bookmark .
     * @param {Element} element SEModel  Element
     * @returns {Boolean}
     */
    isBookmark: function (element) {
        return !!element && (element.getType() === 'BookmarkStart' || element.getType() === 'BookmarkEnd');
    },

    /**
     *   Element Table .
     * Table Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isTable: function (element) {
        return !!element && element.getType() === 'Table';
    },

    /**
     *   Element TableRow .
     * TableRow Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isTableRow: function (element) {
        return !!element && element.getType() === 'TableRow';
    },

    /**
     *   Element TableCell .
     * TableCell Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isTableCell: function (element) {
        return !!element && element.getType() === 'TableCell';
    },

    /**
     *   Element Picture .
     * Picture Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isPicture: function (element) {
        return !!element && element.getType() === 'Picture';
    },

    /**
     *   Element HorizontalLine .
     * HorizontalLine Element true .
     * @param {Element} element SEModel  Element
     * @returns {Boolean} 
     */
    isHorizontalLine: function (element) {
        return !!element && element.getType() === 'HorizontalLine';
    },

    /**
     *   Run Element index  split . <br/>
     * index Run  (0) , Run      .
     * @param {Element} run
     * @param {Number} index
     * @returns {Array} runs
     */
    splitRun: function(run, index) {
        if(index === 0 || index === run.length()) {
            return [run];
        } else {
            return [run, run.split(index)];
        }
    }
};

export default ModelUtil;


