import $ from "jquery";

/**
 *  Popup .
 * Popup    PopupInterface  
 *   .
 * @class PopupInterface
 */
class PopupInterface {
    /**
     * PopupInterface  .
     *  editorId Popup Element .
     * @param {String} editorId -     
     */
    constructor(editorId) {
        this.editorId = editorId;
        this.$element = null;
    }

    /**
     * Popup Container  Popup Dom  .
     * @private
     */
    __createDom__() {
        //PopupInterface      .
    }

    /**
     * Popup Element  .
     * @param {Object} position
     */
    setPosition(position) {
        this.$element.css(position);
    }

    /**
     *     .
     * @private
     */
    __confirm__() {
        this.hide();
    }

    /**
     *     .
     * @private
     */
    __cancel__() {
        this.hide();
    }

    /**
     * Popup Element .
     * @param {HTMLElement} elTarget
     */
    show(elTarget) {
        if (elTarget) {
            const $editor = $('#' + this.editorId);
            const $popupContainer = $editor.find('.se-popup-container');
            const $main = $editor.find('.se-main');
            const $arrow = this.$element.find('.se-popup-arrow');

            const overflowX = elTarget.offsetLeft + elTarget.offsetWidth + this.$element.get(0).offsetWidth - $main.get(0).offsetWidth;
            const popupOffsetTop = elTarget.offsetTop + elTarget.offsetHeight - $popupContainer.get(0).offsetTop;
            const popupOffsetLeft = (overflowX > 0 ? elTarget.offsetLeft - overflowX : elTarget.offsetLeft) - $popupContainer.get(0).offsetLeft;
            const arrowOffsetLeft = elTarget.offsetLeft + (elTarget.offsetWidth / 2) - ($arrow.get(0).offsetWidth / 2) - popupOffsetLeft - $popupContainer.get(0).offsetLeft;

            $arrow.css('left', arrowOffsetLeft);

            this.setPosition({top: popupOffsetTop, left: popupOffsetLeft});
        }

        this.$element.addClass('show');
    }

    /**
     * Popup Element .
     */
    hide() {
        this.$element.removeClass('show');
    }

    /**
     * Popup Element .
     * @param {HTMLElement} elTarget
     */
    toggle(elTarget) {
        if (this.$element.hasClass('show')) {
            this.hide();
        } else {
            this.show(elTarget);
        }
    }

    /**
     * Popup Element .
     */
    getElement() {
        return this.$element;
    }
}

export default PopupInterface;


